import assert from "assert";

import { useEffect } from "react";

import { useQuery } from "@rollouthq/api-client-react/";
import { useOAuth } from "@rollouthq/connect-react/components/automation-form/use-oauth";

export function useCredentialHandler(opts: {
  appKey: string | undefined;
  setCredentialId: (newCredentialId: string) => void;
}) {
  const { appKey, setCredentialId } = opts;

  const { openOAuthPopup } = useOAuth({
    onCredentialAdded: (newCredentialId) => {
      setCredentialId(newCredentialId);
      credentialsQuery.refetch();
    },
  });

  const authInfoQuery = useQuery(["apps.authInfo", { appKey: appKey! }], {
    enabled: appKey != null,
  });

  const authInfo = authInfoQuery.data;
  const credentialsQuery = useQuery(
    ["apps.credentials", { appKey: appKey!, authType: authInfo?.type }],
    {
      enabled: appKey != null && authInfo != null,
    }
  );

  const credentials = credentialsQuery.data;
  useEffect(() => {
    if (authInfo == null || credentials == null) {
      return;
    }

    if (
      authInfo.type === "none" ||
      (authInfo.type === "apiKey" && authInfo.singleCredentialOnly)
    ) {
      const hasCredentials = credentials.length > 0;
      assert(hasCredentials, "No credentials found!");
      return setCredentialId(credentials[0].id);
    }

    if (
      (authInfo.type === "oauth1" || authInfo.type === "oauth2") &&
      authInfo.singleCredentialOnly
    ) {
      if (credentials.length === 0) {
        openOAuthPopup({ appKey: appKey! });
      } else {
        setCredentialId(credentials[0].id);
      }
    }
  }, [authInfo, credentials]);
}
