import React from "react";

import { useRolloutApiBaseUrlContext } from "@rollouthq/connect-react/contexts/api-base-url-context";
import { CSS, st } from "@rollouthq/ui";

export type AppIconProps = {
  appKey: string;
  css?: CSS;
};

export function AppIcon(props: AppIconProps) {
  const { apiBaseUrl } = useRolloutApiBaseUrlContext();

  const { appKey, css } = props;
  const src = `${apiBaseUrl}/connect-react/${appKey}/icon`;

  return <st.img key={appKey} src={src} css={{ width: "1em", ...css }} />;
}
