import React, { useEffect, useState } from "react";

import {
  InferQueryOutput,
  useMutation,
  useQuery,
  useTRPCContext,
} from "@rollouthq/api-client-react";
import { AutomationsList } from "@rollouthq/connect-react/components/automations-list";
import { Plus } from "@rollouthq/ui";

type AutomationsListItem = InferQueryOutput<"automations.list">[number];

export type AutomationFilterData = {
  name?: string;
  trigger: {
    appKey?: string;
    triggerKey?: string;
    inputParams?: Record<string, any>;
  };
  action: {
    appKey?: string;
    actionKey?: string;
    inputParams?: Record<string, any>;
  };
};

export type MyAutomationsProps = {
  onEditAutomation: (automation: AutomationsListItem) => void;
  onCreateAutomation: () => void;
  filter?: (automation: AutomationFilterData) => boolean;
};

export function MyAutomations(props: MyAutomationsProps) {
  const { onEditAutomation, onCreateAutomation, filter } = props;
  const { invalidateQueries } = useTRPCContext();
  const [automationData, setAutomationData] = useState<AutomationsListItem[]>(
    () => []
  );

  const automations = useQuery(["automations.list"]);

  useEffect(() => {
    if (automations.data != null) {
      if (filter != null) {
        setAutomationData(
          automations.data.filter((a: AutomationsListItem) => {
            try {
              return filter(a as AutomationFilterData);
            } catch (filterError) {
              console.error(filterError);
              return false;
            }
          })
        );
      } else {
        setAutomationData(automations.data);
      }
    }
  }, [automations.data, filter]);

  const deleteAutomation = useMutation("automations.delete", {
    onSuccess: () => invalidateQueries(["automations.list"]),
  });

  return (
    <div>
      <div className="rollout-automations-list__header">
        <span className="rollout-automations-list__header-title">
          My automations
        </span>

        <button
          type="button"
          aria-label="New automation button"
          className="rollout-automations-list__new-btn"
          onClick={() => onCreateAutomation()}
        >
          <Plus />
          New
        </button>
      </div>

      <AutomationsList
        automations={automationData ?? []}
        isLoading={automations.isFetching}
        isDeleting={deleteAutomation.isLoading}
        onDeleteAutomation={(at) => deleteAutomation.mutate({ id: at.id })}
        onEditAutomation={onEditAutomation}
      />
    </div>
  );
}
