import assert from "assert";

import React from "react";

import { ActionAppKeyField } from "./action-app-key-input";
import { ActionCredentialIdField } from "./action-credential-id-input";
import { ActionKeyField } from "./action-key-select-input";
import { AutomationFormActionInputs } from "./automation-form-action-inputs";
import { useAutomationFormContext } from "./context";
import { renderField } from "./helpers/render-field";

export function AutomationFormAction() {
  const { data, renderFields } = useAutomationFormContext();
  const { action } = data;

  assert(
    typeof renderFields?.action !== "function" &&
      typeof renderFields?.action !== "boolean"
  );

  const actionAppKeyField = renderField({
    defaultField: ActionAppKeyField,
    renderer: renderFields?.action?.appKey,
    ctx: { ActionAppKeyField },
  });

  const actionKeyField = renderField({
    defaultField: ActionKeyField,
    renderer: renderFields?.action?.actionKey,
    ctx: { ActionKeyField },
  });

  const credentialIdField = renderField({
    defaultField: ActionCredentialIdField,
    renderer: renderFields?.action?.credentialId,
    ctx: { ActionCredentialIdField },
  });

  return (
    <>
      <div className="rollout-automation-form__action">
        <div className="rollout-automation-form__label">Select action</div>

        <div className="rollout-automation-form__helper-text">
          This is what your automation will do when triggered
        </div>

        {actionAppKeyField}
        {actionKeyField}
        {credentialIdField}
      </div>

      {action?.actionKey != null && action.credentialId != null && (
        <AutomationFormActionInputs />
      )}
    </>
  );
}
