import { useCallback } from "react";
import { get, getPath, ObjPathProxy, set } from "ts-object-path";
import { SafeParseReturnType, ZodIssue } from "zod";

import { Meta } from "@rollouthq/api/@types/zod";
import { inputDefToZodObject } from "@rollouthq/common";

import { useAppComponentRendererContext } from "./app-component-renderer-context";
import { getAutomationInputParamSchema } from "./use-automation-input-param-schema";

export function useAutomationInputParam<T extends any>(
  bind: ObjPathProxy<any, T>
): {
  path: PropertyKey[];
  value: T;
  setValue: (newVal: T) => void;
  isOptional: boolean;
  label: string;
  description: string;
  isCredentialDependent: boolean;
  error: ZodIssue | undefined;
} {
  const ctx = useAppComponentRendererContext();
  const {
    inputParams,
    setInputParams,
    importedData,
    credentialId,
    isBlueprintActivation,
  } = ctx;

  const path = getPath(bind);
  const value = get(inputParams, bind) as T;

  const setValue = useCallback(
    (newValue: T) => {
      const newInputParams = structuredClone(inputParams);
      set(newInputParams, bind, newValue);
      setInputParams(newInputParams);
    },
    [setInputParams, inputParams]
  );

  const inputParamsSchema =
    "inputParamsSchema" in importedData
      ? importedData.inputParamsSchema
      : inputDefToZodObject(importedData.inputParamDefs);

  const schema = getAutomationInputParamSchema(inputParamsSchema, bind);
  const isOptional = schema.isOptional();
  const meta = schema.description as any as Meta | undefined;
  const label = meta?.label ?? pathToLabel(path);
  const description = meta?.description ?? "";
  const isCredentialDependent = meta?.isCredentialDependent ?? false;

  const validation = inputParamsSchema.safeParse(inputParams);
  const error =
    isCredentialDependent && credentialId == null && !isBlueprintActivation
      ? undefined
      : getAutomationInputParamValidation(validation, bind);

  return {
    value,
    setValue,
    isOptional,
    label,
    description,
    isCredentialDependent,
    error,
    path,
  };
}

function getAutomationInputParamValidation(
  validation: SafeParseReturnType<any, any>,
  bind: ObjPathProxy<any, any> | undefined
) {
  const path = getPath(bind!);

  if (validation.success) {
    return undefined;
  }

  return validation.error.issues.find((err) => {
    return err.path.join(".") === path.join(".");
  });
}

function pathToLabel(path: PropertyKey[]) {
  return path
    .map((prop) => camelCaseToSpaceSeparated(prop.toString()))
    .join(" ");
}

function camelCaseToSpaceSeparated(str: string) {
  const spaceSeparated = str.replace(
    /([A-Z\d])/g,
    (char) => ` ${char.toLowerCase()}`
  );

  return `${spaceSeparated[0].toUpperCase()}${spaceSeparated.slice(1)}`;
}
