import React from "react";

import { InferQueryOutput, useQuery } from "@rollouthq/api-client-react";
import { BlueprintsList } from "@rollouthq/connect-react/components/blueprints-list";

type BlueprintListItem = InferQueryOutput<"blueprints.list">[number];

export type MyBlueprintsProps = {
  onOpenActivationForm: (blueprint: BlueprintListItem) => void;
};

export function MyBlueprints(props: MyBlueprintsProps) {
  const { onOpenActivationForm } = props;

  const blueprints = useQuery(["blueprints.list"]);

  return (
    <div>
      <div className="rollout-automations-list__header">
        <span className="rollout-automations-list__header-title">
          My blueprints
        </span>
      </div>

      <BlueprintsList
        blueprints={blueprints.data ?? []}
        isLoading={blueprints.isFetching}
        onOpenActivationForm={onOpenActivationForm}
      />
    </div>
  );
}
