import React, { useState } from "react";

import {
  AutomationCreator,
  AutomationCreatorProps,
} from "@rollouthq/connect-react/components/automation-creator";
import { AutomationEditor } from "@rollouthq/connect-react/components/automation-editor";
import { AutomationFormProps } from "@rollouthq/connect-react/components/automation-form";
import {
  MyAutomations,
  MyAutomationsProps,
} from "@rollouthq/connect-react/components/my-automations";
import { ArrowBack } from "@rollouthq/ui";

export type AutomationsManagerProps = {
  onAutomationCreated?: AutomationCreatorProps["onAutomationCreated"];
  onBeforeAutomationCreate?: AutomationCreatorProps["onBeforeAutomationCreate"];
  prefilled?: AutomationCreatorProps["prefilled"];
  filter?: MyAutomationsProps["filter"];
  renderFields?: AutomationFormProps["renderFields"];
};

type AutomationsManagerView =
  | { mode: "LIST" }
  | { mode: "CREATE" }
  | { mode: "EDIT"; automationId: string };

export function AutomationsManager(props: AutomationsManagerProps) {
  const {
    onAutomationCreated,
    onBeforeAutomationCreate,
    prefilled,
    filter,
    renderFields,
  } = props;

  const [currentView, setCurrentView] = useState<AutomationsManagerView>({
    mode: "LIST",
  });

  return (
    <div className="rollout-automations-manager">
      {currentView.mode === "LIST" && (
        <MyAutomations
          onEditAutomation={(aut) =>
            setCurrentView({ mode: "EDIT", automationId: aut.id })
          }
          onCreateAutomation={() => setCurrentView({ mode: "CREATE" })}
          filter={filter}
        />
      )}

      {(currentView.mode === "CREATE" || currentView.mode === "EDIT") && (
        <div className="rollout-automations-manager__navbar">
          <button
            type="button"
            aria-label="Return to automations list button"
            className="rollout-automations-manager__back-btn"
            onClick={() => setCurrentView({ mode: "LIST" })}
          >
            <ArrowBack className="rollout-automation-form__header-back__arrow" />
          </button>
        </div>
      )}
      {currentView.mode === "CREATE" && (
        <AutomationCreator
          onBeforeAutomationCreate={onBeforeAutomationCreate}
          onAutomationCreated={(createdData) => {
            onAutomationCreated?.(createdData);
            setCurrentView({ mode: "LIST" });
          }}
          prefilled={prefilled}
          renderFields={renderFields}
        />
      )}
      {currentView.mode === "EDIT" && (
        <AutomationEditor
          automationId={currentView.automationId}
          onAutomationUpdated={() => setCurrentView({ mode: "LIST" })}
          onAutomationDeleted={() => setCurrentView({ mode: "LIST" })}
          renderFields={renderFields}
        />
      )}
    </div>
  );
}
