import React, { useEffect, useState } from "react";

import { InferQueryOutput, useMutation } from "@rollouthq/api-client-react";
import { Spinner } from "@rollouthq/ui";

import { TokenInput } from "./token-input";

type CredentialDetailsProps = {
  appKey: string;
  authType: InferQueryOutput<"apps.authInfo">;
  saveApiToken: (apiToken: Record<string, string>) => void;
  credentialId: string | undefined;
  firstApiTokenInput: React.MutableRefObject<HTMLInputElement | null> | null;
  openOAuthPopup: (opts: {
    appKey: string;
    customParams?: Record<string, any>;
  }) => Promise<void>;
  closeModal: () => void;
  savingApiToken: boolean;
};

export function CredentialDetails(props: CredentialDetailsProps) {
  const {
    appKey,
    saveApiToken,
    authType,
    firstApiTokenInput,
    openOAuthPopup,
    closeModal,
    savingApiToken,
  } = props;

  const [inputValues, setInputValues] = useState<Record<string, string>>({});

  const [apiTokenConnectionTested, setApiTokenConnectionTested] =
    useState(false);

  const [apiTokenConnectionValid, setApiTokenConnectionValid] = useState(false);
  const [testingConnection, setTestingConnection] = useState(false);

  const apiTokenConnectionQuery = useMutation("credentials.test-connection");
  const apiTokenFormIsValid = validateApiTokenForm();

  // When api token is reset
  useEffect(() => {
    if (apiTokenConnectionTested) {
      setApiTokenConnectionTested(false);
      setApiTokenConnectionValid(false);
    }
  }, [inputValues]);

  function validateApiTokenForm() {
    if ("customParams" in authType && authType.customParams != null) {
      return authType.customParams.every((i) => inputValues?.[i.key] != null);
    }

    return true;
  }

  function testApiTokenConnection() {
    setTestingConnection(true);
    apiTokenConnectionQuery.mutate(
      { appKey: appKey, apiToken: inputValues },
      {
        onSuccess: (valid) => {
          setApiTokenConnectionTested(true);
          setApiTokenConnectionValid(valid);
          setTestingConnection(false);
        },
      }
    );
  }

  function renderInputs() {
    if (authType == null) {
      return null;
    }
    if ("customParams" in authType) {
      return authType.customParams?.map((input, inputIdx) => (
        <TokenInput
          key={inputIdx}
          ref={inputIdx === 0 ? firstApiTokenInput : undefined}
          definition={input}
          value={inputValues?.[input.key] ?? ""}
          onChange={(newValue) =>
            setInputValues({ ...inputValues, [input.key]: newValue })
          }
          isTestingConnection={testingConnection}
        />
      ));
    }
  }

  const openOAuth = () => {
    openOAuthPopup({
      appKey,
      customParams: inputValues,
    });
    setTestingConnection(false);
    closeModal();
  };

  const testOrSaveToken = () => {
    if (apiTokenConnectionValid && inputValues != null) {
      saveApiToken(inputValues);
    } else {
      testApiTokenConnection();
    }
  };

  return (
    <div className="rollout-automation-form__api-token-input-container">
      {renderInputs()}

      {authType?.type === "apiKey" ? (
        <div className="rollout-automation-form__api-token-apitoken-button-container">
          <button
            type="button"
            aria-label="Save button"
            disabled={
              !apiTokenFormIsValid || testingConnection || savingApiToken
            }
            className={`rollout-button--primary ${
              testingConnection
                ? "rollout-automation-form__api-token-test-button"
                : "rollout-automation-form__api-token-save-button"
            }`}
            onClick={testOrSaveToken}
          >
            {(testingConnection || savingApiToken) && <Spinner />}
            {apiTokenConnectionTested && apiTokenConnectionValid
              ? "Save"
              : testingConnection
              ? "Testing Connection ..."
              : "Test Connection"}
          </button>

          {apiTokenConnectionTested && (
            <span className="rollout-automation-form__api-token-connection-result">
              {apiTokenConnectionValid
                ? " Connection Success"
                : " Woops! Check your credentials"}
            </span>
          )}
        </div>
      ) : (
        <button
          type="button"
          aria-label="Save button"
          disabled={!apiTokenFormIsValid || testingConnection || savingApiToken}
          className="rollout-button--primary"
          onClick={openOAuth}
        >
          Add account
        </button>
      )}
    </div>
  );
}
