import React from "react";

import { useQuery } from "@rollouthq/api-client-react";
import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import { FieldProps } from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { useRolloutAppKeyContext } from "@rollouthq/connect-react/contexts/end-user-context";
import { findIssue } from "@rollouthq/connect-react/utils/find-zod-issue";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import {
  Center,
  ComboBox,
  ComboBoxItem,
  FormControl,
  Rocket,
} from "@rollouthq/ui";

import { useAutomationFormContext } from "../context";

type TriggerAppKeyInputProps = {
  isDisabled?: boolean;
};

export function TriggerAppKeyInput(props: TriggerAppKeyInputProps) {
  const { appKey } = useRolloutAppKeyContext();
  const { data, setData, validation } = useAutomationFormContext();
  const { trigger } = data;
  const triggerApps = useQuery(["triggers.apps"]);
  const { shouldShowErrors } = useFormContext();
  const error = findIssue(validation, (b) => b.trigger);
  const options = triggerApps.data ?? [];
  const selectedApp = options?.find((opt) => opt.key === trigger?.appKey);

  return (
    <div className="rollout-automation-form__trigger-app">
      <FormControl error={shouldShowErrors ? error?.message : undefined}>
        <ComboBox<typeof options[number]>
          aria-label="Trigger app dropdown"
          defaultItems={options}
          selectedOption={selectedApp}
          menuTrigger="focus"
          icon={
            trigger == null ? <Rocket /> : <AppIcon appKey={trigger.appKey} />
          }
          isDisabled={props.isDisabled}
          inputPlaceholder="Select app"
          onSelectionChange={(newSelectedTriggerApp) => {
            return setData((currentData) => {
              if (newSelectedTriggerApp == null) {
                return {
                  ...currentData,
                  trigger: undefined,
                };
              }

              const isNewTriggerAppCustomers =
                newSelectedTriggerApp.key === appKey;
              const isActionAppCustomers =
                currentData.action?.appKey === appKey;

              const bothAppsAreCustomers =
                isNewTriggerAppCustomers && isActionAppCustomers;

              const bothAppsAreThirdParty =
                !isNewTriggerAppCustomers && !isActionAppCustomers;

              // If both trigger and action are customer's app, unset action
              const shouldUnsetAction =
                bothAppsAreCustomers || bothAppsAreThirdParty;

              return {
                ...currentData,
                trigger: {
                  appKey: newSelectedTriggerApp.key,
                  inputParams: {},
                },
                action: shouldUnsetAction ? undefined : currentData.action,
              };
            });
          }}
        >
          {(option) => (
            <ComboBoxItem key={option._comboboxKey} textValue={option.name}>
              <Center className="rollout-listbox__option-icon">
                <AppIcon appKey={option.key} />
              </Center>
              {option.name}
            </ComboBoxItem>
          )}
        </ComboBox>
      </FormControl>
    </div>
  );
}

export function TriggerAppKeyField(props: FieldProps) {
  return <TriggerAppKeyInput isDisabled={props.disabled} />;
}
