import { useMemo } from "react";
import { AnyZodObject, z } from "zod";

import {
  PartialActionRef,
  PartialTriggerRef,
} from "@rollouthq/connect-react/components/automation-form/types";

export const zBlueprintActivatorFormSchema = z.object({
  name: z.string().min(1),
  trigger: z.object({
    appKey: z.string(),
    triggerKey: z.string(),
    credentialId: z.string(),
    inputParams: z.object({}),
  }),
  action: z.object({
    appKey: z.string(),
    actionKey: z.string(),
    credentialId: z.string(),
    inputParams: z.object({}),
  }),
});

export type BlueprintActivatorFormData = z.output<
  typeof zBlueprintActivatorFormSchema
>;

export type BlueprintActivatorFormDataInput = z.input<
  typeof zBlueprintActivatorFormSchema
>;

export type PartialBlueprintActivatorData = {
  name?: string | undefined;
  trigger?: PartialTriggerRef;
  action?: PartialActionRef;
};

export function useValidateBlueprintActivatorFormData(opts: {
  data: any;
  triggerInputParamsSchema: AnyZodObject;
  actionInputParamsSchema: AnyZodObject;
}) {
  const { data = {}, actionInputParamsSchema, triggerInputParamsSchema } = opts;

  const schema = useMemo(
    () =>
      zBlueprintActivatorFormSchema.extend({
        trigger: zBlueprintActivatorFormSchema.shape.trigger.extend({
          inputParams: triggerInputParamsSchema,
        }),
        action: zBlueprintActivatorFormSchema.shape.action.extend({
          inputParams: actionInputParamsSchema,
        }),
      }),
    [actionInputParamsSchema, triggerInputParamsSchema]
  );

  return useMemo(() => {
    return schema.safeParse(data);
  }, [data, schema]);
}
