import React, { useMemo } from "react";

import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import { FieldProps } from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { useRolloutAppKeyContext } from "@rollouthq/connect-react/contexts/end-user-context";
import { findIssue } from "@rollouthq/connect-react/utils/find-zod-issue";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import { Center, ComboBox, ComboBoxItem, FormControl } from "@rollouthq/ui";

import { useAutomationFormContext } from "./context";

type ActionAppKeyInputProps = {
  isDisabled?: boolean;
};

function ActionAppKeyInput(props: ActionAppKeyInputProps) {
  const { appKey } = useRolloutAppKeyContext();
  const { apps, data, setData, validation } = useAutomationFormContext();
  const { action } = data;

  const error = findIssue(validation, (b) => b.action);

  const options = useMemo(
    () => apps.data?.filter((a) => a.hasActions) ?? [],
    [apps.data]
  );
  const selectedApp = apps.data?.find((a) => a.key === action?.appKey);

  const { shouldShowErrors } = useFormContext();

  return (
    <div className="rollout-automation-form__action-app">
      {/* Preload app icons by rendering them in a hidden div */}
      <div style={{ display: "none" }}>
        {apps.data?.map((app) => (
          <AppIcon key={app.key} appKey={app.key} />
        ))}
      </div>

      <FormControl error={shouldShowErrors ? error?.message : undefined}>
        <ComboBox<typeof options[number]>
          aria-label="Action dropdown"
          defaultItems={options}
          selectedOption={selectedApp}
          isDisabled={props.isDisabled}
          icon={
            selectedApp == null ? undefined : (
              <AppIcon appKey={selectedApp.key} />
            )
          }
          inputPlaceholder="Select app"
          menuTrigger="focus"
          onSelectionChange={(newSelectedApp) => {
            return setData((currentData) => {
              if (newSelectedApp == null) {
                return {
                  ...currentData,
                  action: undefined,
                };
              }

              const isNewActionAppCustomers = newSelectedApp.key === appKey;
              const isTriggerAppCustomers =
                currentData.trigger?.appKey === appKey;

              const bothAppsAreCustomers =
                isNewActionAppCustomers && isTriggerAppCustomers;

              const bothAppsAreThirdParty =
                !isNewActionAppCustomers && !isTriggerAppCustomers;

              // If both trigger and action are customer's app, unset action
              const shouldUnsetTrigger =
                bothAppsAreCustomers || bothAppsAreThirdParty;

              return {
                ...currentData,
                action: {
                  appKey: newSelectedApp.key,
                  inputParams: {},
                },
                trigger: shouldUnsetTrigger ? undefined : currentData.trigger,
              };
            });
          }}
        >
          {(app) => (
            <ComboBoxItem key={app._comboboxKey} textValue={app.name}>
              <Center className="rollout-listbox__option-icon">
                <AppIcon appKey={app.key} />
              </Center>
              {app.name}
            </ComboBoxItem>
          )}
        </ComboBox>
      </FormControl>
    </div>
  );
}

export function ActionAppKeyField(props: FieldProps) {
  return <ActionAppKeyInput isDisabled={props.disabled} />;
}
