import { FunctionComponent } from "react";
import { ObjPathProxy } from "ts-object-path";
import { AnyZodObject, z } from "zod";

import { TRPCClient, useQuery } from "@rollouthq/api-client-react";
import { CustomerTriggerDropdownInputOption } from "@rollouthq/api/engine/customers/get-customer-trigger-dropdown-options";
import { InputDefOutput } from "@rollouthq/api/engine/inputs/inputs-configuration";
import { Variable } from "@rollouthq/common";
import * as inputComponents from "@rollouthq/connect-react/components/action-inputs";
import {
  FieldComponent,
  RenderCbOrBool,
} from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { createContext } from "@rollouthq/ui";

export type AppComponentProps<Params extends AnyZodObject> = {
  /**
   * Proxy object used to bind `inputParams` properties to inputs.
   * Can't be used to access the actual data!
   * @example
   * <RichTextInput bind={b.to} label="To" />
   */
  b: ObjPathProxy<z.infer<Params>, z.infer<Params>>;
  credentialId: string;
  inputParams: z.infer<Params>;
  setInputParams: (newParams: z.infer<Params>) => void;
  api: TRPCClient;
  useQuery: typeof useQuery;
  inputComponents: typeof inputComponents;
};

export type AppComponentRendererContext = {
  api: TRPCClient;
  credentialId: string;
  setInputParams: (newParams: Record<string, any>) => void;
  inputParams: Record<string, any>;
  variables: readonly Variable[];
  getDynamicOptions: (opts: {
    inputKey: string;
  }) => Promise<CustomerTriggerDropdownInputOption[]>;
  importedData:
    | {
        inputParamsSchema: AnyZodObject;
        Component: FunctionComponent<AppComponentProps<AnyZodObject>>;
      }
    | {
        inputParamDefs: InputDefOutput[];
      };
  isBlueprintActivation?: boolean;
  disabled?: boolean;
  renderInputParams:
    | {
        [param: string]: RenderCbOrBool<{ InputParamField: FieldComponent }>;
      }
    | undefined;
};

export const [
  AppComponentRendererContextProvider,
  useAppComponentRendererContext,
] = createContext<AppComponentRendererContext>({
  name: "AppComponentRenderer",
});
