import React, { FunctionComponent, useLayoutEffect } from "react";
import { useEffect, useState } from "react";
import { AnyZodObject } from "zod";

import { isVariable, z } from "@rollouthq/common";
import * as ActionInputs from "@rollouthq/connect-react/components/action-inputs";
import { AppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { PartialTriggerRef } from "@rollouthq/connect-react/components/automation-form/types";
import { useRolloutApiBaseUrlContext } from "@rollouthq/connect-react/contexts/api-base-url-context";

const useSafeLayoutEffect =
  typeof document === "undefined" ? useEffect : useLayoutEffect;

export type UseTriggerRendererDataParams = PartialTriggerRef | undefined;

export type UseTriggerRendererData =
  | AppComponentRendererContext["importedData"]
  | undefined;

export function useTriggerRendererData(
  opts: UseTriggerRendererDataParams
): UseTriggerRendererData {
  const { apiBaseUrl } = useRolloutApiBaseUrlContext();

  const [triggerRendererData, setTriggerRendererData] =
    useState<UseTriggerRendererData>();

  useSafeLayoutEffect(() => {
    Object.assign(window, {
      ROLLOUT_CONNECT_DEPS: {
        zod: { z },
        react: React,
        ["@rollouthq/common"]: { z, isVariable },
        ["@rollouthq/connect-react/components/action-inputs"]: ActionInputs,
      },
    });
  }, []);

  useSafeLayoutEffect(() => {
    if (opts == null) {
      return setTriggerRendererData(undefined);
    }

    if (!opts.appKey || !opts.triggerKey) {
      return;
    }

    const importPath = `${apiBaseUrl}/connect-react/${opts.appKey}/triggers/${opts.triggerKey}.js`;
    // Workaround to preserve es2020 dynamic import instead of transpiling it
    // equivalent for:
    // import(importPath).then(setTriggerRendererData);
    const fnImport = new Function("path", "return import(path)");

    fnImport(importPath).then(
      (data: {
        Component: FunctionComponent;
        inputParamsSchema: AnyZodObject;
      }) => {
        setTriggerRendererData(data);
      }
    );
  }, [JSON.stringify(opts)]);

  return triggerRendererData;
}
