import popup from "popup-centered";
import { useCallback, useEffect, useState } from "react";

import { useTRPCContext } from "@rollouthq/api-client-react";

export function useOAuth(opts: {
  onCredentialAdded?: (credentialId: string) => void;
}) {
  const [oauthPopupError, setOauthPopupError] = useState<string | null>(null);
  const [currentAppKey, setCurrentAppKey] = useState<string>();
  const { client } = useTRPCContext();

  useEffect(() => {
    function handleNewOauthCredentialMessage(e: MessageEvent<any>) {
      const evData = e.data._rollouthq;

      if (evData?.ok) {
        if (
          evData?.type === "credential-added" &&
          evData.appKey === currentAppKey &&
          evData.credentialId != null
        ) {
          opts.onCredentialAdded?.(evData.credentialId);
        }
      } else {
        if (evData?.error) {
          setOauthPopupError(evData.error);
        }
      }
    }

    window.addEventListener("message", handleNewOauthCredentialMessage, false);

    return () => {
      window.removeEventListener("message", handleNewOauthCredentialMessage);
    };
  }, [currentAppKey]);

  const openOAuthPopup = useCallback(
    async (popupOpts: {
      appKey: string;
      customParams?: Record<string, any>;
    }) => {
      setCurrentAppKey(popupOpts.appKey);
      setOauthPopupError(null);

      try {
        const { url } = await client.query("oauth.getURL", {
          appKey: popupOpts.appKey,
          customParams: popupOpts.customParams,
        });

        popup(url, "", {
          width: 800,
          height: 800,
        });
      } catch (err) {
        setOauthPopupError("Woops! Something went wrong linking account.");
      }
    },
    []
  );

  return {
    oauthPopupError,
    openOAuthPopup,
  };
}
