import { getPath, ObjPathProxy } from "ts-object-path";
import {
  AnyZodObject,
  ZodFirstPartySchemaTypes,
  ZodObject,
  ZodOptional,
  ZodRecord,
} from "zod";

export function getAutomationInputParamSchema(
  inputParamsSchema: AnyZodObject,
  bind: ObjPathProxy<any, any>
) {
  const bindPath = getPath(bind);
  const prop = getByPath(inputParamsSchema, bindPath);
  return prop;
}

function getByPath(
  schema: ZodFirstPartySchemaTypes,
  path: PropertyKey[]
): ZodFirstPartySchemaTypes {
  const restPath = path.slice(1);
  const unwrappedType =
    schema instanceof ZodOptional ? schema.unwrap() : schema;

  if (unwrappedType instanceof ZodObject) {
    const prop = unwrappedType.shape[path[0]];
    return path.length > 1 ? getByPath(prop, restPath) : prop;
  }

  if (unwrappedType instanceof ZodRecord) {
    const prop = unwrappedType.valueSchema;
    return path.length > 1 ? getByPath(prop, restPath) : prop;
  }

  console.error("getByPath: unhandled prop type:", unwrappedType);
  throw new Error(`getByPath: unhandled prop type`);
}
