import React from "react";

import type { PlainTextInputDef } from "@rollouthq/api/engine/inputs/inputs-configuration";
import { TextField } from "@rollouthq/ui/";

export type PlainTextInputDefRendererProps = {
  def: PlainTextInputDef;
  value: string | undefined;
  setValue: (newValue: string | undefined) => void;
  isDisabled: boolean | undefined;
};

export function PlainTextInputDefRenderer(
  props: PlainTextInputDefRendererProps
) {
  return (
    <TextField
      className="rollout-input"
      value={props.value ?? ""}
      onChange={props.setValue}
      isDisabled={props.isDisabled}
    />
  );
}
