import React from "react";

import { FieldProps } from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { findIssue } from "@rollouthq/connect-react/utils/find-zod-issue";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import { TextField } from "@rollouthq/ui";

import { useAutomationFormContext } from "./context";

export function NameInput(props: { disabled?: boolean }) {
  const { disabled = false } = props;
  const { data, setData, validation } = useAutomationFormContext();
  const { shouldShowErrors } = useFormContext();
  const hasError = findIssue(validation, (b) => b.name) != null;

  return (
    <TextField
      aria-label="Automation Name Input"
      className="rollout-automation-form__name"
      placeholder="Give your automation a name"
      isDisabled={disabled}
      value={data.name ?? ""}
      validationState={shouldShowErrors && hasError ? "invalid" : "valid"}
      onChange={(newName) =>
        setData((currentData) => ({
          ...currentData,
          name: newName,
        }))
      }
    />
  );
}

export function NameField(props: FieldProps) {
  return <NameInput {...props} />;
}
