import React, {
  createElement,
  FunctionComponent,
  ReactNode,
  VoidFunctionComponent,
} from "react";

import { Card } from "@rollouthq/connect-react/components/card";

export type RenderCb<Ctx extends {}> = (
  ctx: Ctx & {
    createElement: typeof createElement;
    Card: typeof Card;
  }
) => ReactNode;

export type RenderCbOrBool<Ctx extends {}> = RenderCb<Ctx> | boolean;

export type FieldProps = {
  disabled?: boolean;
};

export type FieldComponent = FunctionComponent<FieldProps>;

export type TriggerRenderCb = RenderCbOrBool<{
  TriggerAppKeyField: FieldComponent;
  TriggerKeyField: FieldComponent;
}>;

export type ActionRenderCb = RenderCbOrBool<{
  ActionAppKeyField: FieldComponent;
  ActionKeyField: FieldComponent;
  ActionCredentialIdField: FieldComponent;
  ActionInputFields: FieldComponent;
}>;

export type RenderFields = {
  name?: RenderCbOrBool<{ NameField: FieldComponent }>;
  trigger?:
    | TriggerRenderCb
    | {
        appKey?: RenderCbOrBool<{ TriggerAppKeyField: FieldComponent }>;
        triggerKey?: RenderCbOrBool<{ TriggerKeyField: FieldComponent }>;
        inputParams?: {
          [param: string]: RenderCbOrBool<{ InputParamField: FieldComponent }>;
        };
      };
  action?:
    | ActionRenderCb
    | {
        appKey?: RenderCbOrBool<{ ActionAppKeyField: FieldComponent }>;
        actionKey?: RenderCbOrBool<{ ActionKeyField: FieldComponent }>;
        credentialId?: RenderCbOrBool<{
          ActionCredentialIdField: FieldComponent;
        }>;
        inputParams?: {
          [param: string]: RenderCbOrBool<{ InputParamField: FieldComponent }>;
        };
      };
};

export function renderField<Ctx extends {}>(opts: {
  defaultField: VoidFunctionComponent<{}>;
  ctx: Ctx;
  renderer: RenderCbOrBool<Ctx> | undefined;
}) {
  const { defaultField: DefaultField, ctx, renderer } = opts;

  if (renderer == null || renderer === true) {
    return <DefaultField />;
  }

  if (renderer === false) {
    return null;
  }

  return renderer({
    ...ctx,
    createElement,
    Card,
  });
}
