import assert from "assert";

import React, { useEffect, useMemo, useRef, useState } from "react";
import { ZodIssue } from "zod";

import {
  InferQueryOutput,
  useMutation,
  useQuery,
  UseQueryResult,
} from "@rollouthq/api-client-react";
import { CredentialDetails } from "@rollouthq/connect-react/components/automation-form/helpers/credential-details";
import { useOAuth } from "@rollouthq/connect-react/components/automation-form/use-oauth";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import {
  Avatar,
  Center,
  ComboBox,
  ComboBoxItem,
  dataAttr,
  FormControl,
  GoogleSignIn,
  GreenPlus,
  Person,
  Plus,
} from "@rollouthq/ui";

const ADD_NEW_CREDENTIAL_OPTION = {
  id: "ADD_OPTION",
  label: "Add new account",
  avatar: undefined,
  icon: <Plus />,
} as const;

export type CredentialInputProps = {
  appKey: string;
  authInfoQuery: UseQueryResult<InferQueryOutput<"apps.authInfo">>;
  setCredentialId: (newCredentialId: string | undefined) => void;
  credentialId: string | undefined;
  error: ZodIssue | undefined;
  isDisabled?: boolean;
};

export function CredentialInput(props: CredentialInputProps) {
  const {
    isDisabled,
    appKey,
    setCredentialId,
    credentialId,
    authInfoQuery,
    error,
  } = props;

  const { shouldShowErrors } = useFormContext();
  const [oauthError, setOauthError] = useState(false);
  const [savingApiToken, setSavingApiToken] = useState(false);
  const apiTokenQuery = useMutation("credentials.add-api-token");
  const credentials = useQuery(["apps.credentials", { appKey }]);
  const firstApiTokenInput = useRef<HTMLInputElement | null>(null);
  const credentialInputRef = useRef<HTMLInputElement | null>(null);
  const [displayCredentialDetails, setDisplayCredentialDetails] =
    useState(false);

  const { openOAuthPopup, oauthPopupError } = useOAuth({
    onCredentialAdded: (newCredentialId) => {
      setCredentialId(newCredentialId);
      credentials.refetch();
    },
  });

  // setAuthError on popupError
  useEffect(() => {
    setOauthError(oauthPopupError != null);
  }, [oauthPopupError]);

  const credentialsList = useMemo(
    () => [...(credentials.data ?? []), ADD_NEW_CREDENTIAL_OPTION],
    [credentials.data]
  );

  const selectedCredential = credentialsList.find(
    (cred) => cred.id === credentialId
  );

  const icon = displayCredentialDetails ? (
    <GreenPlus />
  ) : selectedCredential && selectedCredential.avatar != null ? (
    <Avatar
      src={selectedCredential.avatar}
      name={selectedCredential.label}
      alt={selectedCredential.label}
    />
  ) : (
    <Person />
  );

  function saveApiToken(apiToken: Record<string, string>) {
    if (apiToken != null) {
      setSavingApiToken(true);
      apiTokenQuery.mutate(
        {
          appKey,
          credential: apiToken,
        },
        {
          onSuccess: (t) => {
            setDisplayCredentialDetails(false);
            credentials.refetch();
            credentialInputRef.current?.blur();
            setCredentialId(t);
            setSavingApiToken(false);
          },
          onError: () => setSavingApiToken(false),
        }
      );
    }
  }

  const options = credentialsList;
  const selectedOption = options.find((opt) => opt.id === credentialId);

  return (
    <div
      className="rollout-automation-form__credentials"
      data-avatar
      data-selected={dataAttr(selectedCredential?.avatar != null)}
    >
      <FormControl error={shouldShowErrors ? error?.message : undefined}>
        <ComboBox<typeof options[number]>
          aria-label="Account dropdown"
          customRef={credentialInputRef}
          defaultItems={options}
          selectedOption={selectedOption}
          menuTrigger="focus"
          icon={icon}
          isDisabled={isDisabled}
          isLoading={credentials.isFetching || authInfoQuery.isFetching}
          inputPlaceholder={
            displayCredentialDetails ? "Connect new account" : "Select account"
          }
          onSelectionChange={(newCred) => {
            if (newCred == null) {
              return setCredentialId(undefined);
            }

            const appAuthInfo = authInfoQuery.data;
            const addNewAccount = newCred === ADD_NEW_CREDENTIAL_OPTION;

            assert(appAuthInfo != null, "No app auth info");

            if (addNewAccount) {
              if (
                appAuthInfo.type === "oauth1" ||
                appAuthInfo.type === "oauth2"
              ) {
                if (appAuthInfo.customParams != null) {
                  setDisplayCredentialDetails(true);
                  setTimeout(() => firstApiTokenInput.current?.focus());
                } else {
                  openOAuthPopup({ appKey });
                }
              }

              if (appAuthInfo.type === "apiKey") {
                setDisplayCredentialDetails(true);
                setTimeout(() => firstApiTokenInput.current?.focus());
              }

              setOauthError(false);
              return;
            }

            setOauthError(false);
            setDisplayCredentialDetails(false);
            setCredentialId(newCred.id);
          }}
        >
          {(item) => {
            const isGoogleApp =
              appKey === "gmail" ||
              appKey === "googleCalendar" ||
              appKey === "googleDocs" ||
              appKey === "googleSheets";

            if (isGoogleApp && item.id === "ADD_OPTION") {
              return (
                <ComboBoxItem key={item._comboboxKey} textValue={item.label}>
                  <GoogleSignIn />
                </ComboBoxItem>
              );
            } else {
              return (
                <ComboBoxItem key={item._comboboxKey} textValue={item.label}>
                  <Center className="rollout-listbox__option-icon">
                    <Avatar
                      aria-label="Option icon"
                      src={item.avatar}
                      name={"icon" in item ? null : item.label}
                      alt={item.label}
                      icon={"icon" in item ? item.icon : undefined}
                    />
                  </Center>
                  {item.label}
                </ComboBoxItem>
              );
            }
          }}
        </ComboBox>
      </FormControl>

      {oauthError && (
        <div className="rollout-automation-form-oauth__error__message">
          {oauthPopupError ?? "Oops! Couldn't link that account."}
        </div>
      )}

      {displayCredentialDetails && authInfoQuery.data != null && (
        <CredentialDetails
          appKey={appKey}
          authType={authInfoQuery.data}
          saveApiToken={saveApiToken}
          savingApiToken={savingApiToken}
          credentialId={credentialId}
          firstApiTokenInput={firstApiTokenInput}
          openOAuthPopup={openOAuthPopup}
          closeModal={() => setDisplayCredentialDetails(false)}
        />
      )}
    </div>
  );
}

export type AnyAuthCredentialInputProps = {
  appKey: string;
  authInfoQuery: UseQueryResult<InferQueryOutput<"apps.authInfo">>;
  setCredentialId: (newCredentialId: string | undefined) => void;
  credentialId: string | undefined;
  error: ZodIssue | undefined;
  isDisabled?: boolean;
};

export function AnyAuthCredentialInput(props: AnyAuthCredentialInputProps) {
  const {
    appKey,
    authInfoQuery,
    credentialId,
    error,
    setCredentialId,
    isDisabled,
  } = props;

  const authInfo = authInfoQuery.data;

  if (authInfo == null) {
    return null;
  }

  if (
    (authInfo.type === "oauth1" ||
      authInfo.type === "oauth2" ||
      authInfo.type === "apiKey") &&
    authInfo.singleCredentialOnly
  ) {
    return null;
  }

  if (authInfo.type === "none") {
    return null;
  }

  return (
    <CredentialInput
      appKey={appKey}
      authInfoQuery={authInfoQuery}
      credentialId={credentialId}
      setCredentialId={setCredentialId}
      error={error}
      isDisabled={isDisabled}
    />
  );
}
