import React, { useCallback, useMemo } from "react";

import { resolveMentions } from "@rollouthq/common";
import { ActionInputFormControl } from "@rollouthq/connect-react/components/action-inputs/helpers/action-input-form-control";
import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import {
  FieldProps,
  renderField,
} from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import {
  RichEditorProps,
  RichEditorContent,
  RichEditor,
  createContext,
} from "@rollouthq/ui";

import { ActionInputProps } from "./types";

export type RichTextInputProps = ActionInputProps<
  RichEditorContent | undefined
> & {
  inputProps?: Omit<RichEditorProps, "value" | "setValue">;
  isRequired?: boolean;
  disabled?: boolean;
};

const [RichTextInputContextProvider, useRichTextInputContext] =
  createContext<RichTextInputProps>({
    name: "RichTextInput",
  });

export function RichTextInput(props: RichTextInputProps) {
  const param = useAutomationInputParam(props.bind);
  const paramName = param.path.join(".");
  const { renderInputParams: renderParams } = useAppComponentRendererContext();

  const field = renderField({
    defaultField: RichTextInputField,
    renderer: renderParams?.[paramName],
    ctx: { InputParamField: RichTextInputField },
  });

  return (
    <RichTextInputContextProvider value={props}>
      {field}
    </RichTextInputContextProvider>
  );
}

function RichTextInputField(props: FieldProps) {
  const ctx = useRichTextInputContext();
  const { bind, inputProps, placeholder } = ctx;
  const param = useAutomationInputParam(bind);
  const label = ctx.label ?? param.label;
  const description = ctx.description ?? param.description;
  const appComponentRendererCtx = useAppComponentRendererContext();
  const { variables } = appComponentRendererCtx;
  const { shouldShowErrors } = useFormContext();
  const disabled =
    appComponentRendererCtx.disabled || ctx.disabled || props.disabled;
  const isRequired = !param.isOptional || ctx.isRequired;

  const setValue = useCallback(
    (val: RichEditorContent | null) => {
      param.setValue(val ?? undefined);
    },
    [param.setValue]
  );

  const resolvedValue = useMemo(
    () => param.value && resolveMentions(param.value, variables),
    [param.value, variables]
  );

  return (
    <ActionInputFormControl
      bind={bind}
      label={label}
      isRequired={isRequired}
      description={description}
      error={shouldShowErrors ? param.error?.message : undefined}
    >
      <div className="rollout-rich-text-input">
        <RichEditor
          value={resolvedValue ?? null}
          setValue={setValue}
          placeholder={placeholder}
          formattingEnabled={false}
          linksEnabled={false}
          mentionVariables={variables}
          isDisabled={disabled}
          {...inputProps}
        />
      </div>
    </ActionInputFormControl>
  );
}
