import React from "react";
import { unreachableCase } from "ts-assert-unreachable";

import type { CustomerTriggerDropdownInputOption } from "@rollouthq/api/engine/customers/get-customer-trigger-dropdown-options";
import type { InputDefOutput } from "@rollouthq/api/engine/inputs/inputs-configuration";
import { Variable } from "@rollouthq/common";
import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { PlainTextInputDefRenderer } from "@rollouthq/connect-react/components/app-component-renderer/plain-text-input-def-renderer";
import { RichTextInputDefRenderer } from "@rollouthq/connect-react/components/app-component-renderer/rich-text-input-def-renderer";
import {
  FieldProps,
  renderField,
} from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { StyledInputFormControl } from "@rollouthq/connect-react/components/styled-input-form-control";
import { createContext } from "@rollouthq/ui/";

import { DropdownInputDefRenderer } from "./dropdown-input-def-renderer";

export type InputDefRendererProps = {
  def: InputDefOutput;
  value: any;
  setValue: (newValue: any) => void;
  variables: readonly Variable[];
  getDynamicOptions: (opts: {
    inputKey: string;
  }) => Promise<CustomerTriggerDropdownInputOption[]>;
};

const [InputDefRendererContextProvider, useInputDefRendererContext] =
  createContext<InputDefRendererProps>({
    name: "InputDefRenderer",
  });

export function InputDefRenderer(props: InputDefRendererProps) {
  const { renderInputParams: renderParams } = useAppComponentRendererContext();

  const field = renderField({
    defaultField: InputDefRendererField,
    renderer: renderParams?.[props.def.key],
    ctx: { InputParamField: InputDefRendererField },
  });

  return (
    <InputDefRendererContextProvider value={props}>
      {field}
    </InputDefRendererContextProvider>
  );
}

export function InputDefRendererField(props: FieldProps) {
  const ctx = useInputDefRendererContext();
  const { def, value, setValue, variables, getDynamicOptions } = ctx;

  function renderInput() {
    if (def.type === "plainText") {
      return (
        <PlainTextInputDefRenderer
          def={def}
          value={value}
          setValue={setValue}
          isDisabled={props.disabled}
        />
      );
    }

    if (def.type === "dropdown") {
      return (
        <DropdownInputDefRenderer
          def={def}
          value={value}
          setValue={setValue}
          getDynamicOptions={getDynamicOptions}
          isDisabled={props.disabled}
        />
      );
    }

    if (def.type === "string") {
      return (
        <RichTextInputDefRenderer
          def={def}
          value={value}
          setValue={setValue}
          variables={variables}
          isDisabled={props.disabled}
        />
      );
    }

    unreachableCase(def);
  }

  return (
    <StyledInputFormControl label={def.label} isRequired error={undefined}>
      {renderInput()}
    </StyledInputFormControl>
  );
}
