import { matchSorter } from "match-sorter";
import React, { useEffect, useState } from "react";

import { InferQueryOutput } from "@rollouthq/api-client-react";
import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import {
  isChildofInteractiveElement,
  Center,
  Search,
  TextField,
  Spinner,
  dataAttr,
} from "@rollouthq/ui";
import { useToggle, Trash } from "@rollouthq/ui";

type AutomationsListItem = InferQueryOutput<"automations.list">[number];

export type AutomationsListProps = {
  automations: AutomationsListItem[];
  isLoading?: boolean;
  isDeleting?: boolean;
  onEditAutomation: (automation: AutomationsListItem) => void;
  onDeleteAutomation: (automation: AutomationsListItem) => void;
};

export function AutomationsList(props: AutomationsListProps) {
  const {
    automations,
    isLoading = false,
    isDeleting = false,
    onEditAutomation,
    onDeleteAutomation,
  } = props;

  const [query, setQuery] = useState("");

  const automationFiltered = matchSorter(automations, query, {
    keys: ["name", "action.app.name"],
    threshold: matchSorter.rankings.ACRONYM,
  });

  const isEmptyAutomations = automationFiltered.length === 0;

  return (
    <div className="rollout-automations-list__container">
      <div className="rollout-automations-list__search-input__group">
        <div className="rollout-automations-list__search-input__left-element">
          <Search />
        </div>

        <TextField
          aria-label="Search automations text field"
          placeholder="Search automations"
          value={query}
          onChange={setQuery}
          className="rollout-automations-list__search-input"
        />
      </div>

      {isLoading && (
        <div className="rollout__loading_spinner_container">
          <Spinner className="rollout__loading_spinner" />
        </div>
      )}

      {!isLoading && isEmptyAutomations && (
        <div className="rollout-automations-list__empty-msg">
          No automations found
        </div>
      )}

      <div className="rollout-automations-list__list">
        {automationFiltered.map((automation) => (
          <AutomationsListItem
            key={automation.id}
            automation={automation}
            onEdit={() => onEditAutomation(automation)}
            onDelete={() => onDeleteAutomation(automation)}
            isDeleting={isDeleting}
          />
        ))}
      </div>
    </div>
  );
}

type AutomationsListItemProps = {
  automation: AutomationsListItem;
  onEdit: () => void;
  onDelete: () => void;
  isDeleting: boolean;
};

function AutomationsListItem(props: AutomationsListItemProps) {
  const { automation, onEdit, onDelete, isDeleting } = props;
  const [isOpen, toggleDeleteMenu] = useToggle();
  const [itemDeleting, setItemDeleting] = useState(false);

  useEffect(() => {
    if (!isDeleting && itemDeleting) {
      setItemDeleting(false);
    }
  }, [isDeleting]);

  const optionsList = isOpen && (
    <div
      className="rollout-automations-list-item__options__content"
      onMouseLeave={() => {
        if (isOpen) {
          toggleDeleteMenu();
        }
      }}
    >
      <span>Really delete?</span>
      <button
        type="button"
        aria-label="Confirm deletion button"
        onClick={() => {
          onDelete();
          setItemDeleting(true);
        }}
        className="rollout-automations-list-item__options__delete-item-btn"
      >
        Delete
      </button>

      <button
        type="button"
        aria-label="Cancel deletion button"
        onClick={toggleDeleteMenu}
        className="rollout-automations-list-item__options__cancel-item-btn"
      >
        Cancel
      </button>
    </div>
  );

  return (
    <div
      className="rollout-automations-list__item-wrapper"
      data-deleting={dataAttr(itemDeleting)}
    >
      {optionsList}
      <div
        className="rollout-automations-list__item"
        role="group"
        data-open={dataAttr(isOpen)}
        onClick={(e) => {
          if (!isChildofInteractiveElement(e.target as Element)) {
            onEdit();
          }
        }}
      >
        <div className="rollout-automations-list-item__header">
          <div className="rollout-automations-list-item__icons">
            <Center className="rollout-automations-list-item__trigger">
              <AppIcon appKey={automation.trigger.appKey} />
            </Center>
            <Center className="rollout-automations-list-item__action">
              <AppIcon appKey={automation.action.appKey} />
            </Center>
          </div>
          <div
            role="button"
            className="rollout-automations-list-item__options__trigger"
            onClick={toggleDeleteMenu}
          >
            <Trash />
          </div>
        </div>
        <div className="rollout-automations-list-item__name">
          {automation.name}
        </div>
      </div>
    </div>
  );
}
