import React, { useLayoutEffect } from "react";
import { useEffect, useState } from "react";

import { isVariable, z } from "@rollouthq/common";
import * as ActionInputs from "@rollouthq/connect-react/components/action-inputs";
import { AppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { PartialActionRef } from "@rollouthq/connect-react/components/automation-form/types";
import { useRolloutApiBaseUrlContext } from "@rollouthq/connect-react/contexts/api-base-url-context";

const useSafeLayoutEffect =
  typeof document === "undefined" ? useEffect : useLayoutEffect;

export type UseActionRendererDataOpts = {
  actionRef: PartialActionRef | undefined;
};

export type UseActionRendererData = AppComponentRendererContext["importedData"];

export function useActionRendererData(opts: UseActionRendererDataOpts) {
  const { actionRef } = opts;
  const { apiBaseUrl } = useRolloutApiBaseUrlContext();

  const [actionRendererData, setActionRendererData] = useState<
    UseActionRendererData | undefined
  >();

  useSafeLayoutEffect(() => {
    Object.assign(window, {
      ROLLOUT_CONNECT_DEPS: {
        zod: { z },
        react: React,
        ["@rollouthq/common"]: { z, isVariable },
        ["@rollouthq/connect-react/components/action-inputs"]: ActionInputs,
      },
    });
  }, []);

  useSafeLayoutEffect(() => {
    if (actionRef != null) {
      const { appKey, actionKey } = actionRef;
      if (!appKey || !actionKey) {
        return;
      }

      const importPath = `${apiBaseUrl}/connect-react/${appKey}/actions/${actionKey}.js`;
      // Workaround to preserve es2020 dynamic import instead of transpiling it
      // equivalent for:
      // import(importPath).then(setActionRendererData);
      const fnImport = new Function("path", "return import(path)");
      fnImport(importPath).then((data: UseActionRendererData) =>
        setActionRendererData(data)
      );
    }
  }, [actionRef?.appKey, actionRef?.actionKey]);

  return actionRendererData;
}
