import React, { ReactElement, useEffect } from "react";

import { ActionInputFormControl } from "@rollouthq/connect-react/components/action-inputs/helpers/action-input-form-control";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import { useFormContext } from "@rollouthq/connect-react/utils/form";

import { ActionInputProps } from "./types";

type FindByInputProps = ActionInputProps<string | undefined> & {
  filterInput: ReactElement;
  filterValueInput: ReactElement;
  selectInput: ReactElement;
  resource: string;
};

export function FindBy(props: FindByInputProps) {
  const { filterInput, filterValueInput, selectInput } = props;
  const { bind } = props;
  const param = useAutomationInputParam(bind);
  const { shouldShowErrors } = useFormContext();
  const { value, setValue, isOptional } = param;
  const label = props.label ?? param.label;
  const description = props.description ?? param.description;

  useEffect(() => {
    if (value == null) {
      setValue("filter");
    }
  }, [value]);

  return (
    <>
      <ActionInputFormControl
        bind={bind}
        label={label}
        isRequired={!isOptional}
        description={description}
        error={shouldShowErrors ? param.error?.message : undefined}
      >
        <div className="rollout-form-control-find__container">
          <button
            type="button"
            aria-label="Search from list button"
            className={
              value == "select"
                ? "rollout-form-control-find__active"
                : "rollout-form-control-find__disabled"
            }
            onClick={() => setValue("select")}
          >
            From list
          </button>
          <button
            type="button"
            aria-label="Search by filter button"
            className={
              value == "filter"
                ? "rollout-form-control-find__active"
                : "rollout-form-control-find__disabled"
            }
            onClick={() => setValue("filter")}
          >
            From search
          </button>
        </div>
      </ActionInputFormControl>

      {value == "filter" && filterInput}
      {value == "filter" && filterValueInput}
      {value == "select" && selectInput}
    </>
  );
}
