import React, { useCallback, useMemo } from "react";
import { useState } from "react";
import { useSpinDelay } from "spin-delay";

import { useQuery, useTRPCContext } from "@rollouthq/api-client-react";
import { inputDefToZodObject } from "@rollouthq/common";
import { useTriggerRendererData } from "@rollouthq/connect-react/components/automation-form/get-trigger-renderer-data";
import { useRolloutAllowedAppsContext } from "@rollouthq/connect-react/contexts/allowed-apps-context";
import { useActionsQuery } from "@rollouthq/connect-react/hooks/use-actions-query";
import { useAppsQuery } from "@rollouthq/connect-react/hooks/use-apps-query";
import { useCredentialHandler } from "@rollouthq/connect-react/hooks/use-credential-handler";
import { FormContextProvider } from "@rollouthq/connect-react/utils/form";
import { dataAttr, useToggle, Spinner, useAsyncMemo } from "@rollouthq/ui";

import { ActionAppKeyField } from "./action-app-key-input";
import { ActionCredentialIdField } from "./action-credential-id-input";
import { ActionKeyField } from "./action-key-select-input";
import { AutomationFormAction } from "./automation-form-action";
import { ActionInputFields } from "./automation-form-action-inputs";
import { AutomationFormTrigger } from "./automation-form-trigger";
import { AutomationFormContextProvider } from "./context";
import { useActionRendererData } from "./get-action-renderer-data";
import { RenderFields, renderField } from "./helpers/render-field";
import { TriggerAppKeyField } from "./helpers/trigger-app-key-input";
import { TriggerKeyField } from "./helpers/trigger-key-input";
import { NameField } from "./name-input";
import {
  AutomationFormData,
  PartialAutomationFormData,
  useValidateAutomationFormData,
  zAutomationFormSchema,
} from "./types";

export type AutomationFormProps = {
  data: PartialAutomationFormData;
  onUpdate: (
    updater: (
      currentData: PartialAutomationFormData
    ) => PartialAutomationFormData
  ) => void;
  onSave: (data: AutomationFormData) => void;
  onCancel?: () => void;
  onDelete?: () => void;
  isSaving: boolean;
  mode: "edit" | "create";
  renderFields?: RenderFields;
};

export function AutomationForm(props: AutomationFormProps) {
  const {
    data,
    isSaving,
    onSave,
    onCancel,
    onUpdate,
    onDelete,
    mode,
    renderFields,
  } = props;

  const [shouldShowErrors, setShouldShowErrors] = useState(false);
  const [isOpen, toggleDeleteMenu] = useToggle();
  const { client } = useTRPCContext();

  const triggersQuery = useQuery(
    ["apps.triggers", { appKey: data.trigger?.appKey! }],
    { enabled: data.trigger?.appKey != null }
  );

  const selectedTrigger = triggersQuery.data?.find(
    (t) => t.key === data.trigger?.triggerKey
  );

  const triggerPayloadSchemaAsync = useAsyncMemo(async () => {
    if (selectedTrigger == null) {
      return [];
    }
    if (selectedTrigger.payloadConfiguration.type === "static") {
      return selectedTrigger.payloadConfiguration.staticSchema;
    }
    return await client.query("triggers.getPayloadSchema", {
      appKey: data.trigger?.appKey!,
      triggerKey: data.trigger?.triggerKey!,
      inputParams: data.trigger?.inputParams!,
    });
  }, [
    JSON.stringify(selectedTrigger?.payloadConfiguration),
    JSON.stringify(data.trigger),
  ]);

  const triggerPayloadVariables = useMemo(
    () =>
      (triggerPayloadSchemaAsync.value ?? []).map((prop) => ({
        id: prop.key,
        label: prop.label,
        type: "string" as const,
      })),
    [triggerPayloadSchemaAsync.value]
  );

  const { allowedApps } = useRolloutAllowedAppsContext();

  const apps = useAppsQuery({ allowedApps });
  const actions = useActionsQuery(data.action);
  const actionRendererData = useActionRendererData({ actionRef: data.action });
  const triggerRendererData = useTriggerRendererData(data.trigger);

  const triggerInputParamsSchema = useMemo(() => {
    if (triggerRendererData == null) {
      return zAutomationFormSchema.shape.action.shape.inputParams;
    }
    if ("inputParamsSchema" in triggerRendererData) {
      return triggerRendererData.inputParamsSchema;
    } else {
      return inputDefToZodObject(triggerRendererData.inputParamDefs);
    }
  }, [triggerRendererData]);

  const actionInputParamsSchema = useMemo(() => {
    if (actionRendererData == null) {
      return zAutomationFormSchema.shape.action.shape.inputParams;
    }
    if ("inputParamsSchema" in actionRendererData) {
      return actionRendererData.inputParamsSchema;
    } else {
      return inputDefToZodObject(actionRendererData.inputParamDefs);
    }
  }, [actionRendererData]);

  const validation = useValidateAutomationFormData({
    data,
    triggerInputParamsSchema,
    actionInputParamsSchema,
  });

  const setData = useCallback(
    (
      updater: (
        currentData: PartialAutomationFormData
      ) => PartialAutomationFormData
    ) =>
      onUpdate((currentData) => {
        const newData = updater(currentData);

        const didChangeTrigger =
          currentData.trigger?.triggerKey !== newData.trigger?.triggerKey;

        const newAction =
          newData.action == null
            ? undefined
            : {
                ...newData.action,
                inputParams: didChangeTrigger ? {} : newData.action.inputParams,
              };

        return {
          ...newData,
          action: newAction,
        };
      }),
    [onUpdate]
  );

  const context = useMemo(
    () => ({
      data,
      apps,
      actions,
      validation,
      variables: triggerPayloadVariables,
      setData,
      renderFields,
    }),
    [
      data,
      apps,
      actions,
      validation,
      triggerPayloadVariables,
      setData,
      renderFields,
    ]
  );

  const formContext = useMemo(
    () => ({ shouldShowErrors, setShouldShowErrors }),
    [shouldShowErrors, setShouldShowErrors]
  );

  const setTriggerCredentialId = useCallback(
    (newCredentialId: string) =>
      setData((currentData) => ({
        ...currentData,
        trigger: {
          ...currentData.trigger!,
          credentialId: newCredentialId,
        },
      })),
    [setData]
  );

  const setActionCredentialId = useCallback(
    (newCredentialId: string) =>
      setData((currentData) => ({
        ...currentData,
        action: {
          ...currentData.action!,
          credentialId: newCredentialId,
        },
      })),
    [setData]
  );

  useCredentialHandler({
    appKey: data.trigger?.appKey,
    setCredentialId: setTriggerCredentialId,
  });

  useCredentialHandler({
    appKey: data.action?.appKey,
    setCredentialId: setActionCredentialId,
  });

  const hasNullData = apps.data == null;
  const showSpinner = useSpinDelay(hasNullData, {
    delay: 500,
    minDuration: 200,
  });

  const errorFetchingData = apps.failureCount > 0;

  if (errorFetchingData) {
    return (
      <div className="rollout-automations-list__error-msg">
        Woops! Something went wrong fetching automations.
      </div>
    );
  }

  if (hasNullData || showSpinner) {
    return (
      <div className="rollout__loading_spinner_container">
        <Spinner className="rollout__loading_spinner" />
      </div>
    );
  }

  const nameField = renderField({
    defaultField: NameField,
    renderer: renderFields?.name,
    ctx: { NameField },
  });

  const triggerField =
    typeof renderFields?.trigger === "object" ? (
      <AutomationFormTrigger />
    ) : (
      renderField({
        defaultField: AutomationFormTrigger,
        renderer: renderFields?.trigger,
        ctx: { TriggerAppKeyField, TriggerKeyField },
      })
    );

  const actionField =
    typeof renderFields?.action === "object" ? (
      <AutomationFormAction />
    ) : (
      renderField({
        defaultField: AutomationFormAction,
        renderer: renderFields?.action,
        ctx: {
          ActionAppKeyField,
          ActionKeyField,
          ActionCredentialIdField,
          ActionInputFields,
        },
      })
    );

  return (
    <FormContextProvider value={formContext}>
      <AutomationFormContextProvider value={context}>
        <div className="rollout-automation-form">
          <div className="rollout-automation-form__header">{nameField}</div>

          <div className="rollout-automation-form__content">
            {triggerField}
            {actionField}
          </div>

          <div className="rollout-automation-form__buttons">
            <button
              type="button"
              aria-label="Save button"
              className="rollout-button--primary rollout-automation-form__submit"
              data-loading={dataAttr(isSaving)}
              onClick={() => {
                setShouldShowErrors(true);
                if (validation.success) {
                  onSave(validation.data);
                } else {
                  console.error(validation.error);
                }
              }}
            >
              {isSaving ? "Saving..." : "Save"}
            </button>

            {onCancel != null && (
              <button
                type="button"
                aria-label="Cancel button"
                className="rollout-button--secondary rollout-automation-form__cancel"
                onClick={() => onCancel()}
              >
                Cancel
              </button>
            )}

            {mode !== "create" && (
              <div className="rollout-automation-form__delete">
                {isOpen && (
                  <div className="rollout-automation-form-delete__confirm-dialog">
                    <div className="rollout-automation-form__delete-confirm-text">
                      Are you sure you want to delete this automation?
                    </div>

                    <div className="rollout-automation-form__delete-confirm-buttons">
                      <button
                        type="button"
                        aria-label="Cancel deletion button"
                        onClick={toggleDeleteMenu}
                        className="rollout-button--secondary rollout-automation-form__delete-cancel"
                      >
                        No, cancel
                      </button>

                      <button
                        type="button"
                        aria-label="Confirm deletion button"
                        onClick={onDelete}
                        className="rollout-button--danger rollout-automation-form__delete-confirm"
                      >
                        Yes, delete
                      </button>
                    </div>
                  </div>
                )}

                <button
                  type="button"
                  aria-label="Delete button"
                  className="rollout-button--danger rollout-automations-form-delete__trigger"
                  onClick={toggleDeleteMenu}
                >
                  Delete
                </button>
              </div>
            )}
          </div>
        </div>
      </AutomationFormContextProvider>
    </FormContextProvider>
  );
}
