import { PropsWithChildren, useMemo } from "react";
import React from "react";

import { ThirdPartyAppKey as ThirdPartyAppSlug } from "@rollouthq/api/apps/types";
import { createContext } from "@rollouthq/ui";

export type AnyAppSlug = ThirdPartyAppSlug | "customer";

export type RolloutAllowedAppsContext = {
  allowedApps: AnyAppSlug[] | undefined;
};

const [_RolloutAllowedAppsContextProvider, useRolloutAllowedAppsContext] =
  createContext<RolloutAllowedAppsContext>({
    name: "RolloutAllowedAppsContextProvider",
  });

export { useRolloutAllowedAppsContext };

export function RolloutAllowedAppsContextProvider(
  props: PropsWithChildren<RolloutAllowedAppsContext>
) {
  const { children, ...ctxProps } = props;
  const ctx = useMemo(() => ctxProps, Object.values(ctxProps));

  return (
    <_RolloutAllowedAppsContextProvider value={ctx}>
      {children}
    </_RolloutAllowedAppsContextProvider>
  );
}
