import { useEffect } from "react";

import { useLazyQuery } from "@rollouthq/api-client-react";
import { PartialActionRef } from "@rollouthq/connect-react/components/automation-form/types";

export function useActionsQuery(action: PartialActionRef | undefined) {
  const actions = useLazyQuery("apps.actions");

  useEffect(() => {
    if (action?.appKey != null) {
      actions.fetch({ appKey: action.appKey });
    }
  }, [action?.appKey]);

  return actions;
}
