import React, { forwardRef } from "react";

import { AuthCustomParamInputDefinition } from "@rollouthq/api/engine/define-auth";
import { TextField } from "@rollouthq/ui";

type TokenInputProps = {
  definition: AuthCustomParamInputDefinition;
  isTestingConnection: boolean;
  value: string;
  onChange: (newValue: string) => void;
};

export const TokenInput = forwardRef<HTMLInputElement, TokenInputProps>(
  (props, ref) => {
    const { isTestingConnection, value, onChange } = props;
    const { infoHTML, label, placeholder } = props.definition;

    return (
      <div>
        <label className="rollout-automation-form__api-token-input-label">
          {label}
        </label>

        {infoHTML != null && (
          <div
            dangerouslySetInnerHTML={{ __html: infoHTML }}
            className="rollout-automation-form__api-token-input-info"
          />
        )}

        <div>
          <TextField
            ref={ref}
            className="rollout-automation-form__api-token-input"
            aria-label="Token input text field"
            isDisabled={isTestingConnection}
            placeholder={placeholder}
            value={value}
            onChange={onChange}
          />
        </div>
      </div>
    );
  }
);
