import React, { useState } from "react";

import { InferMutationOutput, useMutation } from "@rollouthq/api-client-react";
import { AutomationCustomData } from "@rollouthq/api/engine/automations/custom-data";
import {
  AutomationForm,
  AutomationFormData,
  AutomationFormProps,
  PartialAutomationFormData,
} from "@rollouthq/connect-react/components/automation-form";

type Automation = InferMutationOutput<"automations.create">;

type OnBeforeAutomationCreatedReturnSync =
  | boolean
  | {
      automationData: AutomationFormData & {
        customData?: AutomationCustomData;
      };
    };

export type PrefilledData = {
  name?: string;
  trigger?: {
    appKey?: string;
    triggerKey?: string;
    inputParams?: Record<string, any>;
    credentialId?: string;
  };
  action?: {
    appKey?: string;
    actionKey?: string;
    inputParams?: Record<string, any>;
    credentialId?: string;
  };
};

export type AutomationCreatorProps = {
  onAutomationCreated: (props: { automation: Automation }) => void;
  onBeforeAutomationCreate?: (props: {
    automationData: AutomationFormData;
  }) =>
    | OnBeforeAutomationCreatedReturnSync
    | Promise<OnBeforeAutomationCreatedReturnSync>;
  onCancel?: () => void;
  prefilled?: PrefilledData;
  renderFields?: AutomationFormProps["renderFields"];
};

export function AutomationCreator(props: AutomationCreatorProps) {
  const {
    onAutomationCreated,
    onBeforeAutomationCreate,
    onCancel,
    prefilled,
    renderFields,
  } = props;

  const createAutomationMutation = useMutation("automations.create");

  const [automationFormData, setAutomationFormData] =
    useState<PartialAutomationFormData>(() => {
      const defaultData: PartialAutomationFormData = {};

      if (prefilled == null) {
        return defaultData;
      }

      if (prefilled.name) {
        defaultData.name = prefilled.name;
      }

      if (prefilled.trigger?.appKey != null) {
        defaultData.trigger = {
          ...prefilled.trigger,
          appKey: prefilled.trigger.appKey,
          inputParams: prefilled.trigger.inputParams ?? {},
        };
      }

      if (prefilled.action?.appKey != null) {
        defaultData.action = {
          ...prefilled.action,
          appKey: prefilled.action.appKey,
          inputParams: prefilled.action.inputParams ?? {},
        };
      }

      return defaultData;
    });

  const createAutomation = async (data: AutomationFormData) => {
    const onBeforeAutomationCreateReturn = await onBeforeAutomationCreate?.({
      automationData: data,
    });

    if (onBeforeAutomationCreateReturn === false) {
      // Abort creating automation
      return;
    }

    const automationData =
      onBeforeAutomationCreateReturn === true ||
      onBeforeAutomationCreateReturn === undefined
        ? data
        : onBeforeAutomationCreateReturn.automationData;

    const automation = await createAutomationMutation.mutateAsync(
      automationData
    );

    onAutomationCreated({ automation });
  };

  return (
    <div className="rollout-automation-creator">
      <AutomationForm
        mode="create"
        data={automationFormData}
        renderFields={renderFields}
        onUpdate={setAutomationFormData}
        isSaving={createAutomationMutation.isLoading}
        onSave={createAutomation}
        onCancel={onCancel}
      />
    </div>
  );
}
