import React from "react";
import { ZodIssue } from "zod";

import { useQuery } from "@rollouthq/api-client-react";
import { AnyAuthCredentialInput } from "@rollouthq/connect-react/components/credential-input";

export type TriggerCredentialIdInputProps = {
  appKey: string;
  credentialId: string | undefined;
  setCredentialId: (newCredentialId: string | undefined) => void;
  error: ZodIssue | undefined;
  isDisabled?: boolean;
};

export function TriggerCredentialIdInput(props: TriggerCredentialIdInputProps) {
  const { appKey, credentialId, setCredentialId, error, isDisabled } = props;

  const authInfoQuery = useQuery(["apps.authInfo", { appKey }]);

  if (authInfoQuery.data == null) {
    return null;
  }

  return (
    <AnyAuthCredentialInput
      appKey={appKey}
      authInfoQuery={authInfoQuery}
      credentialId={credentialId}
      setCredentialId={setCredentialId}
      error={error}
      isDisabled={isDisabled}
    />
  );
}
