import React from "react";

import type { RichTextInputDef } from "@rollouthq/api/engine/inputs/inputs-configuration";
import {
  resolveMentions,
  RichEditorContent,
  Variable,
} from "@rollouthq/common";
import { RichEditor } from "@rollouthq/ui";

export type RichTextInputDefRendererProps = {
  def: RichTextInputDef;
  value: RichEditorContent | undefined;
  setValue: (newValue: RichEditorContent | undefined) => void;
  variables: readonly Variable[];
  isDisabled: boolean | undefined;
};

export function RichTextInputDefRenderer(props: RichTextInputDefRendererProps) {
  const resolvedValue =
    props.value && resolveMentions(props.value, props.variables);

  return (
    <div className="rollout-rich-text-input">
      <RichEditor
        value={resolvedValue ?? null}
        setValue={(newValue) => props.setValue(newValue ?? undefined)}
        formattingEnabled={false}
        linksEnabled={false}
        mentionVariables={props.variables}
        isDisabled={props.isDisabled}
      />
    </div>
  );
}
