import React, { useState } from "react";

import { BlueprintActivator } from "@rollouthq/connect-react/components/blueprint-activator";
import { MyBlueprints } from "@rollouthq/connect-react/components/my-blueprints";
import { ArrowBack } from "@rollouthq/ui";

export type BlueprintsManagerView =
  | { mode: "LIST" }
  | { mode: "ACTIVATE"; blueprintId: string };

export function BlueprintsManager() {
  const [currentView, setCurrentView] = useState<BlueprintsManagerView>({
    mode: "LIST",
  });

  return (
    <div className="rollout-automations-manager">
      {currentView.mode === "LIST" && (
        <MyBlueprints
          onOpenActivationForm={(bp) => {
            setCurrentView({ mode: "ACTIVATE", blueprintId: bp.id });
          }}
        />
      )}

      {currentView.mode === "ACTIVATE" && (
        <>
          <div className="rollout-automations-manager__navbar">
            <button
              type="button"
              aria-label="Return to blueprints list button"
              className="rollout-blueprints-manager__back-btn"
              onClick={() => setCurrentView({ mode: "LIST" })}
            >
              <ArrowBack className="rollout-automation-form__header-back__arrow" />
            </button>
          </div>

          <BlueprintActivator blueprintId={currentView.blueprintId} />
        </>
      )}
    </div>
  );
}
