import React, { useCallback, useEffect, useMemo, useState } from "react";
import { useSpinDelay } from "spin-delay";

import {
  useLazyQuery,
  useMutation,
  useQuery,
  useTRPCContext,
} from "@rollouthq/api-client-react";
import type { AutomationThirdPartyActionRef } from "@rollouthq/api/engine/automations/action-ref";
import { inputDefToZodObject } from "@rollouthq/common/";
import { AppComponentRenderer } from "@rollouthq/connect-react/components/app-component-renderer";
import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import { useActionRendererData } from "@rollouthq/connect-react/components/automation-form/get-action-renderer-data";
import { useTriggerRendererData } from "@rollouthq/connect-react/components/automation-form/get-trigger-renderer-data";
import { PartialActionRef } from "@rollouthq/connect-react/components/automation-form/types";
import { AnyAuthCredentialInput } from "@rollouthq/connect-react/components/credential-input";
import { useCredentialHandler } from "@rollouthq/connect-react/hooks/use-credential-handler";
import { FormContextProvider } from "@rollouthq/connect-react/utils/form";
import { Center, FormControl, Spinner, useAsyncMemo } from "@rollouthq/ui";

import {
  PartialBlueprintActivatorData,
  useValidateBlueprintActivatorFormData,
  zBlueprintActivatorFormSchema,
} from "./types";

export type BlueprintActivatorProps = {
  blueprintId: string;
};

export function BlueprintActivator(props: BlueprintActivatorProps) {
  const { blueprintId } = props;

  const { client, invalidateQueries } = useTRPCContext();

  const [data, setData] = useState<PartialBlueprintActivatorData | undefined>();

  const triggerPayloadSchema = useLazyQuery("triggers.getPayloadSchema");
  const triggerAuthType = useLazyQuery("apps.authInfo");
  const triggersForApp = useLazyQuery("apps.triggers");

  const triggerRendererData = useTriggerRendererData(data?.trigger);

  useEffect(() => {
    const trigger = data?.trigger;

    if (trigger == null) {
      return;
    }

    triggerAuthType.fetch({ appKey: trigger.appKey });
    triggersForApp.fetch({ appKey: trigger.appKey });

    if (trigger.triggerKey != null && trigger.appKey != null) {
      triggerPayloadSchema.fetch({
        appKey: trigger.appKey,
        triggerKey: trigger.triggerKey,
      });
    }
  }, [data?.trigger]);

  const authType = useLazyQuery("apps.authInfo");

  useEffect(() => {
    if (data?.action?.appKey != null) {
      authType.fetch({ appKey: data.action.appKey });
    }
  }, [data?.action?.appKey]);

  const blueprint = useQuery(
    [
      "blueprints.get",
      {
        blueprintId,
      },
    ],
    {
      onSuccess: (bp) => {
        if (data == null) {
          if (bp.automation != null) {
            setData({
              name: bp.automation.name,
              action: bp.automation.action,
              trigger: bp.automation.trigger,
            });
          } else {
            setData({
              name: bp.name,
              action: bp.action,
              trigger: bp.trigger,
            });
          }
        }
      },
    }
  );

  const createAutomation = useMutation("blueprints.createAutomation");
  const deactivateAutomation = useMutation("blueprints.deactivateAutomation");

  const triggerInputParamsSchema = useMemo(() => {
    if (triggerRendererData == null) {
      return zBlueprintActivatorFormSchema.shape.action.shape.inputParams;
    }
    if ("inputParamsSchema" in triggerRendererData) {
      return triggerRendererData.inputParamsSchema;
    } else {
      return inputDefToZodObject(triggerRendererData.inputParamDefs);
    }
  }, [triggerRendererData]);

  const actionRendererData = useActionRendererData({
    actionRef: data?.action,
  });

  const actionInputParamsSchema = useMemo(() => {
    if (actionRendererData == null) {
      return zBlueprintActivatorFormSchema.shape.action.shape.inputParams;
    }
    if ("inputParamsSchema" in actionRendererData) {
      return actionRendererData.inputParamsSchema;
    } else {
      return inputDefToZodObject(actionRendererData.inputParamDefs);
    }
  }, [actionRendererData]);

  const validation = useValidateBlueprintActivatorFormData({
    data,
    triggerInputParamsSchema,
    actionInputParamsSchema,
  });

  const setTriggerCredentialId = useCallback(
    (newCredentialId: string) =>
      setData((currentData) => ({
        ...currentData,
        trigger: {
          ...currentData?.trigger!,
          credentialId: newCredentialId,
        },
      })),
    [setData]
  );

  const setActionCredentialId = useCallback(
    (newCredentialId: string) =>
      setData((currentData) => ({
        ...currentData,
        action: {
          ...currentData?.action!,
          credentialId: newCredentialId,
        },
      })),
    [setData]
  );

  useCredentialHandler({
    appKey: data?.trigger?.appKey,
    setCredentialId: setTriggerCredentialId,
  });

  const setTriggerInputParams = useCallback(
    (newParams: Record<string, any>) => {
      setData((currentData) => ({
        ...currentData,
        trigger: {
          ...currentData!.trigger!,
          inputParams: newParams,
        },
      }));
    },
    [setData]
  );

  const triggersQuery = useQuery(
    ["apps.triggers", { appKey: data?.trigger?.appKey! }],
    { enabled: data?.trigger?.appKey != null }
  );

  const selectedTrigger = triggersQuery.data?.find(
    (t) => t.key === data?.trigger?.triggerKey
  );

  const triggerPayloadSchemaAsync = useAsyncMemo(async () => {
    if (selectedTrigger == null) {
      return [];
    }
    if (selectedTrigger.payloadConfiguration.type === "static") {
      return selectedTrigger.payloadConfiguration.staticSchema;
    }
    return await client.query("triggers.getPayloadSchema", {
      appKey: data?.trigger?.appKey!,
      triggerKey: data?.trigger?.triggerKey!,
      inputParams: data?.trigger?.inputParams!,
    });
  }, [
    JSON.stringify(selectedTrigger?.payloadConfiguration),
    JSON.stringify(data?.trigger),
  ]);

  const triggerPayloadVariables = useMemo(
    () =>
      (triggerPayloadSchemaAsync.value ?? []).map((prop) => ({
        id: prop.key,
        label: prop.label,
        type: "string" as const,
      })),
    [triggerPayloadSchemaAsync.value]
  );

  function maybeRenderActionRender() {
    if (data?.action == null) {
      return null;
    }

    if (actionRendererData == null) {
      return null;
    }

    return (
      <div className="rollout-automation-form__action-inputs">
        <div>
          <div className="rollout-automation-form__label">Set up action</div>

          <div className="rollout-automation-form__credential">
            <FormControl label="Account" isRequired>
              <AnyAuthCredentialInput
                error={
                  validation.success
                    ? undefined
                    : validation.error.issues.find(
                        (issue) =>
                          issue.path.join(".") === "action.credentialId"
                      )
                }
                credentialId={data.action.credentialId}
                setCredentialId={(newCredentialId) => {
                  const newAction: PartialActionRef = {
                    ...(data.action as AutomationThirdPartyActionRef),
                    credentialId: newCredentialId,
                  };

                  setData({
                    ...data,
                    action: newAction,
                  });
                }}
                authInfoQuery={authType}
                appKey={data.action.appKey}
              />
            </FormControl>
          </div>

          <div className="rollout-automation-form__action-inputs-wrapper">
            <AppComponentRenderer
              isBlueprintActivation
              api={client}
              credentialId={data.action.credentialId!}
              inputParams={data.action.inputParams}
              setInputParams={(newParams) =>
                setData({
                  ...data,
                  action: {
                    ...(data.action as PartialActionRef),
                    inputParams: newParams,
                  },
                })
              }
              renderInputParams={undefined}
              importedData={actionRendererData}
              variables={triggerPayloadVariables}
              getDynamicOptions={() => {
                throw new Error("Dynamic options not implemented in actions");
              }}
            />
          </div>
        </div>
      </div>
    );
  }

  const isActivated = blueprint.data?.automation?.active ?? false;

  const [shouldShowErrors, setShouldShowErrors] = useState(false);

  const formContext = useMemo(
    () => ({ shouldShowErrors, setShouldShowErrors }),
    [shouldShowErrors, setShouldShowErrors]
  );

  const triggerInputVariables = useMemo(() => [], []);

  async function activate() {
    setShouldShowErrors(true);
    if (validation.success) {
      const d = validation.data;

      await createAutomation.mutateAsync({
        blueprintId,
        trigger: {
          credentialId: d.trigger.credentialId,
          inputParams: d.trigger.inputParams,
        },
        action: {
          credentialId: d.action.credentialId,
          inputParams: d.action.inputParams,
        },
      });

      invalidateQueries(["blueprints.getAutomation", { blueprintId }]);
      invalidateQueries(["blueprints.get", { blueprintId }]);
      invalidateQueries("blueprints.list");
    } else {
      console.log(data);
      console.error(validation.error);
    }
  }

  async function deactivate() {
    await deactivateAutomation.mutateAsync({ blueprintId });

    invalidateQueries(["blueprints.getAutomation", { blueprintId }]);
    invalidateQueries(["blueprints.get", { blueprintId }]);
    invalidateQueries("blueprints.list");
  }

  const showSpinner = useSpinDelay(data == null, {
    delay: 500,
    minDuration: 200,
  });

  if (data == null) {
    return null;
  }

  if (showSpinner) {
    return (
      <div className="rollout__loading_spinner_container">
        <Spinner className="rollout__loading_spinner" />
      </div>
    );
  }

  const hasInputParams =
    triggerRendererData &&
    ("inputParamsSchema" in triggerRendererData
      ? Object.keys(triggerRendererData.inputParamsSchema.shape).length > 0
      : triggerRendererData.inputParamDefs.length > 0);

  const triggerInputs = data.trigger?.triggerKey != null && (
    <div className="rollout-blueprint-activator__trigger">
      <div className="rollout-blueprint-activator__label">Set up trigger</div>

      <div className="rollout-blueprint-activator__trigger__fields">
        <AnyAuthCredentialInput
          error={
            validation.success
              ? undefined
              : validation.error.issues.find(
                  (issue) => issue.path.join(".") === "trigger.credentialId"
                )
          }
          appKey={data.trigger.appKey}
          setCredentialId={(newCredentialId) =>
            setData({
              ...data,
              trigger: {
                ...data.trigger!,
                credentialId: newCredentialId,
              },
            })
          }
          authInfoQuery={triggerAuthType}
          credentialId={data.trigger.credentialId}
        />

        {data.trigger.credentialId != null && hasInputParams && (
          <div className="rollout-automation-form__trigger-inputs-wrapper">
            <AppComponentRenderer
              api={client}
              credentialId={data.trigger.credentialId}
              inputParams={data.trigger.inputParams}
              setInputParams={setTriggerInputParams}
              renderInputParams={undefined}
              importedData={triggerRendererData}
              variables={triggerInputVariables}
              getDynamicOptions={({ inputKey }) =>
                client.query("triggers.getTriggerInputOptions", {
                  triggerKey: data.trigger?.triggerKey!,
                  inputKey,
                })
              }
            />
          </div>
        )}
      </div>
    </div>
  );

  return (
    <FormContextProvider value={formContext}>
      <div>
        {blueprint.data != null && (
          <div>
            <div className="rollout-blueprint-activator__header">
              <div className="rollout-automations-list-item__icons">
                <Center className="rollout-automations-list-item__trigger">
                  <AppIcon appKey={blueprint.data.trigger.appKey} />
                </Center>
                <Center className="rollout-automations-list-item__action">
                  <AppIcon appKey={blueprint.data.action.appKey} />
                </Center>
              </div>

              <div className="rollout-blueprint-activator__name">
                {blueprint.data.name}
              </div>
            </div>

            {triggerInputs}

            {maybeRenderActionRender()}

            <div>
              {isActivated ? (
                <button
                  type="button"
                  aria-label="Deactivation button"
                  disabled={createAutomation.isLoading}
                  className="rollout-blueprint-activator__deactivate-btn rollout-button--primary"
                  onClick={deactivate}
                >
                  {createAutomation.isLoading
                    ? "Deactivating..."
                    : "Deactivate"}
                </button>
              ) : (
                <button
                  type="button"
                  aria-label="Activation button"
                  disabled={createAutomation.isLoading}
                  className="rollout-blueprint-activator__activate-btn rollout-button--primary"
                  onClick={activate}
                >
                  {createAutomation.isLoading ? "Activating..." : "Activate"}
                </button>
              )}
            </div>
          </div>
        )}
      </div>
    </FormContextProvider>
  );
}
