import React from "react";
import { ReactElement } from "react";

import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import { StyledInputFormControl } from "@rollouthq/connect-react/components/styled-input-form-control";
import { Flex, UserForm } from "@rollouthq/ui";

export type ActionInputFormControlProps = {
  bind: any;
  label?: string;
  description?: string;
  error?: string;
  isRequired?: boolean;
  children: ReactElement;
};

export function ActionInputFormControl(props: ActionInputFormControlProps) {
  const { bind, children, description, error, isRequired = false } = props;

  const { credentialId, isBlueprintActivation = false } =
    useAppComponentRendererContext();

  const param = useAutomationInputParam(bind);

  const isProvidedByUserPlaceholder =
    param.isCredentialDependent &&
    credentialId == null &&
    !isBlueprintActivation;

  if (isBlueprintActivation && !param.isCredentialDependent) {
    return null;
  }

  function getChildren() {
    if (isProvidedByUserPlaceholder) {
      return (
        <Flex
          css={{
            alignItems: "center",
            padding: "8px",
            backgroundColor: "$surfaceSecondary",
            borderRadius: "4px",
            color: "$grayMedium",
            fontSize: "13px",
            fontWeight: 500,
          }}
        >
          <UserForm css={{ marginRight: "6px" }} />
          Input provided by user when automation is activated
        </Flex>
      );
    }

    return children;
  }

  const label = props.label ?? "";

  return (
    <StyledInputFormControl
      description={description}
      error={error}
      isRequired={isRequired}
      label={label}
    >
      {getChildren()}
    </StyledInputFormControl>
  );
}
