import { createProxy, getPath, ObjPathProxy } from "ts-object-path";
import { SafeParseReturnType } from "zod";

export function findIssue<Input, Output>(
  validation: SafeParseReturnType<Input, Output>,
  pathGetter: (b: ObjPathProxy<Input, Input>) => any
) {
  if (validation.success) {
    return undefined;
  }
  const proxy = createProxy<Input>();
  const path = getPath(pathGetter(proxy));

  return validation.error.issues.find((err) => {
    return err.path.join(".") === path.join(".");
  });
}
