import React from "react";

import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import { FieldProps } from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import { Center, ComboBox, ComboBoxItem, FormControl } from "@rollouthq/ui";

import { useAutomationFormContext } from "./context";

export function ActionKeySelectInput(props: { isDisabled?: boolean }) {
  const { data, setData, actions, validation } = useAutomationFormContext();
  const { shouldShowErrors, setShouldShowErrors } = useFormContext();

  const getValidationIssues = () => {
    if (validation.success) {
      return null;
    }
    return validation.error.issues.find(
      (issue) => issue.path.join(".") === "action.actionKey"
    );
  };
  const error = getValidationIssues();
  const icon =
    data.action != null ? <AppIcon appKey={data.action.appKey} /> : undefined;

  const options = actions.data ?? [];
  const selectedAction = actions.data?.find(
    (a) => a.key === data.action?.actionKey
  );

  return (
    <div className="rollout-automation-form__action-action">
      <FormControl error={shouldShowErrors ? error?.message : undefined}>
        <ComboBox<typeof options[number]>
          aria-label="Action dropdown"
          defaultItems={options}
          selectedOption={selectedAction}
          isDisabled={props.isDisabled}
          icon={icon}
          isLoading={actions.isLoading}
          inputPlaceholder="Select action"
          menuTrigger="focus"
          onSelectionChange={(newSelectedAction) => {
            setShouldShowErrors(false);

            return setData((currentData) => ({
              ...currentData,
              action: {
                ...currentData.action!,
                actionKey: newSelectedAction?.key,
                inputParams: {},
              },
            }));
          }}
        >
          {(item) => (
            <ComboBoxItem key={item._comboboxKey} textValue={item.name}>
              <Center className="rollout-listbox__option-icon">{icon}</Center>
              {item.name}
            </ComboBoxItem>
          )}
        </ComboBox>
      </FormControl>
    </div>
  );
}

export function ActionKeyField(props: FieldProps) {
  return <ActionKeySelectInput isDisabled={props.disabled} />;
}
