import assert from "assert";

import React, { useCallback } from "react";

import { useTRPCContext } from "@rollouthq/api-client-react";
import { AppComponentRenderer } from "@rollouthq/connect-react/components/app-component-renderer";
import { useAutomationFormContext } from "@rollouthq/connect-react/components/automation-form";
import { useActionRendererData } from "@rollouthq/connect-react/components/automation-form/get-action-renderer-data";

export function AutomationFormActionInputs() {
  return (
    <div className="rollout-automation-form__action-inputs">
      <div>
        <div className="rollout-automation-form__label">Set up action</div>
        <ActionInputFields />
      </div>
    </div>
  );
}

export function ActionInputFields() {
  const { client } = useTRPCContext();
  const { data, setData, variables, renderFields } = useAutomationFormContext();
  const action = data.action!;
  const actionRendererData = useActionRendererData({ actionRef: data.action });

  const setInputParams = useCallback(
    (newParams: Record<string, any>) => {
      setData((currentData) => ({
        ...currentData,
        action: {
          ...currentData.action!,
          inputParams: newParams,
        },
      }));
    },
    [setData]
  );

  if (actionRendererData == null || action.credentialId == null) {
    return null;
  }

  assert(
    typeof renderFields?.action !== "function" &&
      typeof renderFields?.action !== "boolean"
  );

  return (
    <div className="rollout-automation-form__action-inputs-wrapper">
      <AppComponentRenderer
        api={client}
        credentialId={action.credentialId!}
        inputParams={action.inputParams}
        setInputParams={setInputParams}
        renderInputParams={renderFields?.action?.inputParams}
        importedData={actionRendererData}
        variables={variables}
        getDynamicOptions={() => {
          throw new Error("Dynamic options not implemented in actions");
        }}
      />
    </div>
  );
}
