import assert from "assert";

import React, { useCallback, useMemo } from "react";

import { useTRPCContext } from "@rollouthq/api-client-react";
import { AppComponentRenderer } from "@rollouthq/connect-react/components/app-component-renderer";

import { useAutomationFormContext } from "./context";
import { useTriggerRendererData } from "./get-trigger-renderer-data";
import { renderField } from "./helpers/render-field";
import { TriggerAppKeyField } from "./helpers/trigger-app-key-input";
import { TriggerKeyField } from "./helpers/trigger-key-input";
import { TriggerCredentialIdInput } from "./trigger-credential-id-input";

export function AutomationFormTrigger() {
  const { client } = useTRPCContext();

  const { data, setData, renderFields, validation } =
    useAutomationFormContext();

  const { trigger } = data;

  const triggerRendererData = useTriggerRendererData(trigger);

  const setInputParams = useCallback(
    (newParams: Record<string, any>) => {
      setData((currentData) => ({
        ...currentData,
        trigger: {
          ...currentData.trigger!,
          inputParams: newParams,
        },
      }));
    },
    [setData]
  );

  const setCredentialId = useCallback(
    (newCredentialId: string | undefined) =>
      setData((currentData) => ({
        ...currentData,
        trigger: {
          ...currentData.trigger!,
          credentialId: newCredentialId,
        },
      })),
    [setData]
  );

  const variables = useMemo(() => [], []);

  assert(
    typeof renderFields?.trigger !== "function" &&
      typeof renderFields?.trigger !== "boolean"
  );

  const triggerAppKeyField = renderField({
    defaultField: TriggerAppKeyField,
    renderer: renderFields?.trigger?.appKey,
    ctx: { TriggerAppKeyField },
  });

  const triggerKeyField = renderField({
    defaultField: TriggerKeyField,
    renderer: renderFields?.trigger?.triggerKey,
    ctx: { TriggerKeyField },
  });

  const hasInputParams =
    triggerRendererData &&
    ("inputParamsSchema" in triggerRendererData
      ? Object.keys(triggerRendererData.inputParamsSchema.shape).length > 0
      : triggerRendererData.inputParamDefs.length > 0);

  return (
    <div className="rollout-automation-form__trigger">
      <div className="rollout-automation-form__label">Select trigger</div>

      <div className="rollout-automation-form__helper-text">
        This is the event that will kick off your action
      </div>

      {triggerAppKeyField}

      <div className="rollout-automation-form__trigger-trigger">
        {triggerKeyField}
      </div>

      {trigger?.triggerKey != null && (
        <TriggerCredentialIdInput
          appKey={trigger.appKey}
          credentialId={trigger.credentialId}
          setCredentialId={setCredentialId}
          error={
            validation.success
              ? undefined
              : validation.error.issues.find(
                  (issue) => issue.path.join(".") === "trigger.credentialId"
                )
          }
        />
      )}

      {trigger?.credentialId != null && hasInputParams && (
        <div className="rollout-automation-form__trigger-inputs">
          <div className="rollout-automation-form__trigger-inputs-wrapper">
            <AppComponentRenderer
              api={client}
              credentialId={trigger.credentialId}
              inputParams={trigger.inputParams}
              setInputParams={setInputParams}
              renderInputParams={renderFields?.trigger?.inputParams}
              importedData={triggerRendererData}
              variables={variables}
              getDynamicOptions={({ inputKey }) =>
                client.query("triggers.getTriggerInputOptions", {
                  triggerKey: trigger.triggerKey!,
                  inputKey,
                })
              }
            />
          </div>
        </div>
      )}
    </div>
  );
}
