import React, { useCallback } from "react";
import { ZodIssue } from "zod";

import { useQuery } from "@rollouthq/api-client-react/";
import { AnyAuthCredentialInput } from "@rollouthq/connect-react/components/credential-input";

import { useAutomationFormContext } from "./context";
import { FieldProps } from "./helpers/render-field";

export type ActionCredentialIdInput = {
  appKey: string;
  credentialId: string | undefined;
  setCredentialId: (newCredentialId: string | undefined) => void;
  error: ZodIssue | undefined;
  isDisabled?: boolean;
};

export function ActionCredentialIdInput(props: ActionCredentialIdInput) {
  const { appKey, credentialId, setCredentialId, error, isDisabled } = props;

  const authInfoQuery = useQuery(["apps.authInfo", { appKey }]);

  if (authInfoQuery.data == null) {
    return null;
  }

  return (
    <AnyAuthCredentialInput
      appKey={appKey}
      authInfoQuery={authInfoQuery}
      credentialId={credentialId}
      setCredentialId={setCredentialId}
      error={error}
      isDisabled={isDisabled}
    />
  );
}

export function ActionCredentialIdField(props: FieldProps) {
  const { data, setData, validation } = useAutomationFormContext();

  const { action } = data;

  const setCredentialId = useCallback(
    (newCredentialId: string | undefined) =>
      setData((currentData) => ({
        ...currentData,
        action: {
          ...currentData.action!,
          credentialId: newCredentialId,
        },
      })),
    []
  );

  if (action?.actionKey == null) {
    return null;
  }

  return (
    <ActionCredentialIdInput
      appKey={action.appKey}
      credentialId={action.credentialId}
      setCredentialId={setCredentialId}
      error={
        validation.success
          ? undefined
          : validation.error.issues.find(
              (issue) => issue.path.join(".") === "action.credentialId"
            )
      }
      isDisabled={props.disabled}
    />
  );
}
