import React, { useState } from "react";

import {
  InferQueryOutput,
  useMutation,
  useQuery,
  useTRPCContext,
} from "@rollouthq/api-client-react";
import {
  AutomationForm,
  PartialAutomationFormData,
  AutomationFormData,
  AutomationFormProps,
} from "@rollouthq/connect-react/components/automation-form";

type Automation = NonNullable<InferQueryOutput<"automations.get">>;

type AutomationEditorProps = {
  automationId: string;
  onAutomationDeleted?: () => void;
  onAutomationUpdated?: (automation: Automation) => void;
  onCancel?: () => void;
  renderFields?: AutomationFormProps["renderFields"];
};

export function AutomationEditor(props: AutomationEditorProps) {
  const {
    automationId,
    onAutomationDeleted,
    onAutomationUpdated,
    onCancel,
    renderFields,
  } = props;
  const { invalidateQueries } = useTRPCContext();

  const deleteAutomation = useMutation("automations.delete", {
    onSuccess: () => invalidateQueries(["automations.list"]),
  });

  const [automationFormData, setAutomationFormData] =
    useState<PartialAutomationFormData>({});

  const automation = useQuery(["automations.get", { automationId }], {
    onSuccess(aut) {
      if (aut != null) {
        setAutomationFormData({
          name: aut.name,
          action: aut.action,
          trigger: aut.trigger,
        });
      }
    },
  });

  const updateAutomationMutation = useMutation("automations.update");

  if (automation.isLoading) {
    return null;
  }

  if (automation.data == null) {
    return (
      <div className="rollout-automations-editor__not-found">
        Automation not found
      </div>
    );
  }

  const editAutomation = async (data: AutomationFormData) => {
    const updatedAutomation = await updateAutomationMutation.mutateAsync({
      id: automationId,
      automationData: {
        name: data.name,
        action: data.action,
        trigger: data.trigger,
      },
    });

    onAutomationUpdated?.(updatedAutomation);
  };

  const onDelete = () => {
    deleteAutomation.mutate(
      { id: automation.data.id },
      {
        onSuccess: () => {
          onAutomationDeleted?.();
        },
      }
    );
  };

  return (
    <div className="rollout-automation-editor">
      <AutomationForm
        mode="edit"
        data={automationFormData}
        renderFields={renderFields}
        onUpdate={setAutomationFormData}
        isSaving={updateAutomationMutation.isLoading}
        onSave={editAutomation}
        onDelete={onDelete}
        onCancel={onCancel}
      />
    </div>
  );
}
