import { PropsWithChildren, useMemo } from "react";
import React from "react";

import { createContext } from "@rollouthq/ui";

export type RolloutApiBaseUrlContext = {
  apiBaseUrl: string;
};

const [_RolloutApiBaseUrlContextProvider, useRolloutApiBaseUrlContext] =
  createContext<RolloutApiBaseUrlContext>({
    name: "RolloutApiBaseUrlContext",
  });

export { useRolloutApiBaseUrlContext };

export function RolloutApiBaseUrlContextProvider(
  props: PropsWithChildren<RolloutApiBaseUrlContext>
) {
  const { children, ...ctxProps } = props;
  const ctx = useMemo(() => ctxProps, Object.values(ctxProps));

  return (
    <_RolloutApiBaseUrlContextProvider value={ctx}>
      {children}
    </_RolloutApiBaseUrlContextProvider>
  );
}
