import { SSRProvider } from "@react-aria/ssr";
import {
  BrowserClient,
  defaultIntegrations,
  defaultStackParser,
  makeFetchTransport,
} from "@sentry/browser";
import React, { useCallback, useState } from "react";
import { ReactElement } from "react";
import { ErrorBoundary } from "react-error-boundary";

import { RolloutAPIClientProvider } from "@rollouthq/api-client-react";
import { ErrorFallback } from "@rollouthq/connect-react/components/error-fallback";
import {
  AnyAppSlug,
  RolloutAllowedAppsContextProvider,
} from "@rollouthq/connect-react/contexts/allowed-apps-context";
import { RolloutApiBaseUrlContextProvider } from "@rollouthq/connect-react/contexts/api-base-url-context";
import { RolloutAppKeyProvider } from "@rollouthq/connect-react/contexts/end-user-context";

const ROLLOUT_API_BASE_URL = "https://app.rollouthq.com/api";
const SENTRY_DSN =
  "https://28e3dd4b02f14f2eb74768520edbc8a1@o345148.ingest.sentry.io/6463365";

export type RolloutConnectProviderProps = {
  token: string | (() => Promise<string> | string);
  onError?: (error: Error, info: { componentStack: string }) => void;
  apiBaseUrl?: string | undefined;
  allowedApps?: AnyAppSlug[];
  children: ReactElement;
  env?: string;
};

export function RolloutConnectProvider(props: RolloutConnectProviderProps) {
  const {
    token,
    allowedApps,
    apiBaseUrl = ROLLOUT_API_BASE_URL,
    children,
    env = "production",
    onError: onErrorProp,
  } = props;

  const [sentry] = useState(() => {
    return new BrowserClient({
      dsn: SENTRY_DSN,
      transport: makeFetchTransport,
      stackParser: defaultStackParser,
      integrations: defaultIntegrations,
    });
  });

  const onError = useCallback(
    (error: Error, info: React.ErrorInfo) => {
      onErrorProp?.(error, info);

      if (env === "production") {
        sentry.captureException(error, {
          data: { info },
        });
      }
    },
    [env, sentry, onErrorProp]
  );

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback} onError={onError}>
      <RolloutApiBaseUrlContextProvider apiBaseUrl={apiBaseUrl}>
        <RolloutAllowedAppsContextProvider allowedApps={allowedApps}>
          <RolloutAPIClientProvider
            token={token}
            apiBaseUrl={apiBaseUrl ?? ROLLOUT_API_BASE_URL}
          >
            <RolloutAppKeyProvider>
              <SSRProvider>{children}</SSRProvider>
            </RolloutAppKeyProvider>
          </RolloutAPIClientProvider>
        </RolloutAllowedAppsContextProvider>
      </RolloutApiBaseUrlContextProvider>
    </ErrorBoundary>
  );
}
