import { z } from "zod";

import { InferQueryOutput, UseQueryResult } from "@rollouthq/api-client-react";
import { Variable } from "@rollouthq/common";
import { createContext } from "@rollouthq/ui";

import { RenderFields } from "./helpers/render-field";
import {
  AutomationFormData,
  AutomationFormDataInput,
  PartialAutomationFormData,
} from "./types";

export type AutomationFormContext = {
  data: PartialAutomationFormData;
  setData: (
    updater: (
      currentData: PartialAutomationFormData
    ) => PartialAutomationFormData
  ) => void;
  validation: z.SafeParseReturnType<
    AutomationFormData,
    AutomationFormDataInput
  >;
  apps: UseQueryResult<InferQueryOutput<"apps.list">>;
  actions: UseQueryResult<InferQueryOutput<"apps.actions">>;
  variables: readonly Variable[];
  renderFields: RenderFields | undefined;
};

export const [AutomationFormContextProvider, useAutomationFormContext] =
  createContext<AutomationFormContext>({
    name: "AutomationForm",
  });
