import React, { useEffect, useState } from "react";

import { isVariable, ROLLOUTHQ_VARIABLE_PREFIX } from "@rollouthq/common";
import { ActionInputProps } from "@rollouthq/connect-react/components/action-inputs";
import { ActionInputFormControl } from "@rollouthq/connect-react/components/action-inputs/helpers/action-input-form-control";
import { useAppComponentRendererContext } from "@rollouthq/connect-react/components/app-component-renderer/app-component-renderer-context";
import { useAutomationInputParam } from "@rollouthq/connect-react/components/app-component-renderer/use-automation-input-param";
import {
  FieldProps,
  renderField,
} from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import {
  ComboBox,
  ComboBoxItem,
  ComboBoxProps,
  ComboBoxSection,
  createContext,
} from "@rollouthq/ui";

export type DropdownOption = {
  id: string;
  name: string;
};

export type DropdownInputProps = ActionInputProps<string | undefined> & {
  allowOnlyVariables?: boolean;
  dependencies?: string[];
  isDisabled?: boolean;
  fetchOptions: () =>
    | Promise<readonly DropdownOption[]>
    | readonly DropdownOption[];
} & Omit<
    ComboBoxProps<Record<string, any>>,
    "children" | "defaultItems" | "selectedOption"
  >;

const [DropdownInputContextProvider, useDropdownInputContext] =
  createContext<DropdownInputProps>({
    name: "DropdownInput",
  });

export function DropdownInput(props: DropdownInputProps) {
  const param = useAutomationInputParam(props.bind);
  const paramName = param.path.join(".");
  const { renderInputParams: renderParams } = useAppComponentRendererContext();

  const field = renderField({
    defaultField: DropdownInputField,
    renderer: renderParams?.[paramName],
    ctx: { InputParamField: DropdownInputField },
  });

  return (
    <DropdownInputContextProvider value={props}>
      {field}
    </DropdownInputContextProvider>
  );
}

function DropdownInputField(props: FieldProps) {
  const ctx = useDropdownInputContext();

  const { bind, dependencies, fetchOptions, allowOnlyVariables } = ctx;

  const param = useAutomationInputParam(bind);
  const { value, setValue } = param;
  const label = ctx.label ?? param.label;
  const description = ctx.description ?? param.description;
  const placeholder = ctx.placeholder || "Select option";

  const [options, setOptions] = useState<readonly DropdownOption[]>([]);
  const [isLoadingOptions, setIsLoadingOptions] = useState(false);

  const { shouldShowErrors } = useFormContext();
  const { variables, disabled: disabledFromContext } =
    useAppComponentRendererContext();

  const disabled =
    ctx.isDisabled ||
    disabledFromContext ||
    (dependencies != null && dependencies.every(Boolean) !== true) ||
    props.disabled;

  useEffect(() => {
    async function refetch() {
      setIsLoadingOptions(true);
      const dropDownOptions = await fetchOptions();
      setOptions(dropDownOptions);
      setIsLoadingOptions(false);
    }
    if (!allowOnlyVariables && !disabled) {
      refetch();
    }
  }, [JSON.stringify(dependencies), allowOnlyVariables, disabled]);

  useEffect(() => {
    if (allowOnlyVariables && value && !isVariable(value)) {
      setValue(undefined);
    }
  }, [allowOnlyVariables, value]);

  const variableOptions = variables.map((v) => ({
    id: `${ROLLOUTHQ_VARIABLE_PREFIX}${v.id}`,
    name: `@${v.label}`,
  }));
  const allOptions = [...variableOptions, ...options];
  const selectedOption = allOptions.find((opt) => opt.id === value);

  const variableOptionsDisplay = (
    <ComboBox<typeof variableOptions[number]>
      aria-label="Variable dropdown"
      optionKey="id"
      isDisabled={disabled}
      defaultItems={variableOptions}
      menuTrigger="focus"
      selectedOption={selectedOption}
      isLoading={isLoadingOptions}
      inputPlaceholder={placeholder}
      onSelectionChange={(newItem) => setValue(newItem?.id)}
    >
      <ComboBoxSection title="Variables">
        {variableOptions.map((opt) => (
          <ComboBoxItem key={opt.id}>{opt.name}</ComboBoxItem>
        ))}
      </ComboBoxSection>
    </ComboBox>
  );

  const allVariableOptionsDisplay = (
    <ComboBox<typeof allOptions[number]>
      aria-label="Variable dropdown"
      optionKey="id"
      isDisabled={disabled}
      defaultItems={allOptions}
      menuTrigger="focus"
      selectedOption={selectedOption}
      key={value}
      isLoading={isLoadingOptions}
      inputPlaceholder={placeholder}
      onSelectionChange={(newItem) => setValue(newItem?.id)}
    >
      <ComboBoxSection title="Options">
        {options.map((opt) => (
          <ComboBoxItem key={opt.id}>{opt.name}</ComboBoxItem>
        ))}
      </ComboBoxSection>
      <ComboBoxSection title="Variables">
        {variableOptions.map((opt) => (
          <ComboBoxItem key={opt.id}>{opt.name}</ComboBoxItem>
        ))}
      </ComboBoxSection>
    </ComboBox>
  );

  const optionsDisplay = allowOnlyVariables
    ? variableOptionsDisplay
    : allVariableOptionsDisplay;

  return (
    <ActionInputFormControl
      bind={bind}
      label={label}
      description={description}
      error={shouldShowErrors ? param.error?.message : undefined}
      isRequired={!param.isOptional}
    >
      {optionsDisplay}
    </ActionInputFormControl>
  );
}
