import { useMemo } from "react";
import { AnyZodObject, z } from "zod";

export const zAutomationFormSchema = z.object({
  name: z.string().min(1),
  trigger: z.object({
    appKey: z.string(),
    triggerKey: z.string(),
    credentialId: z.string(),
    inputParams: z.object({}),
  }),
  action: z.object({
    appKey: z.string(),
    actionKey: z.string(),
    credentialId: z.string(),
    inputParams: z.object({}),
  }),
});

export type AutomationFormData = z.output<typeof zAutomationFormSchema>;
export type AutomationFormDataInput = PartialAutomationFormData;

export type MakePartialRef<
  Type extends {},
  RequiredProps extends keyof Type
> = Pick<Type, RequiredProps> & Omit<MaybeUndefinedProps<Type>, RequiredProps>;

export type PartialActionRef = MakePartialRef<
  AutomationFormData["action"],
  "appKey" | "inputParams"
>;

export type PartialTriggerRef = MakePartialRef<
  AutomationFormData["trigger"],
  "appKey" | "inputParams"
>;

export type PartialAutomationFormData = Omit<
  MaybeUndefinedProps<AutomationFormData>,
  "action" | "trigger"
> & {
  action?: PartialActionRef;
  trigger?: PartialTriggerRef;
};

type MaybeUndefinedProps<T> = { [K in keyof T]?: T[K] | undefined };

export function useValidateAutomationFormData(opts: {
  data: PartialAutomationFormData;
  triggerInputParamsSchema: AnyZodObject;
  actionInputParamsSchema: AnyZodObject;
}) {
  const { data, actionInputParamsSchema, triggerInputParamsSchema } = opts;

  const schema = useMemo(
    () =>
      zAutomationFormSchema.extend({
        trigger: zAutomationFormSchema.shape.trigger.extend({
          inputParams: triggerInputParamsSchema,
        }),
        action: zAutomationFormSchema.shape.action.extend({
          inputParams: actionInputParamsSchema,
        }),
      }),
    [triggerInputParamsSchema, actionInputParamsSchema]
  );

  return useMemo(() => {
    return schema.safeParse(data);
  }, [data, schema]);
}
