import React from "react";

import { useQuery } from "@rollouthq/api-client-react";
import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import { FieldProps } from "@rollouthq/connect-react/components/automation-form/helpers/render-field";
import { findIssue } from "@rollouthq/connect-react/utils/find-zod-issue";
import { useFormContext } from "@rollouthq/connect-react/utils/form";
import {
  Center,
  ComboBox,
  ComboBoxItem,
  FormControl,
  Rocket,
} from "@rollouthq/ui";

import { useAutomationFormContext } from "../context";

type TriggerInputProps = {
  isDisabled?: boolean;
};

export function TriggerKeyInput(props: TriggerInputProps) {
  const { isDisabled } = props;
  const { shouldShowErrors } = useFormContext();
  const { validation, data, setData } = useAutomationFormContext();
  const { trigger } = data;

  const triggers = useQuery(["apps.triggers", { appKey: trigger?.appKey! }], {
    enabled: trigger?.appKey != null,
  });

  const error = findIssue(validation, (b) => b.trigger.triggerKey);
  const options = triggers.data ?? [];
  const selectedOption = options.find((opt) => opt.key === trigger?.triggerKey);

  return (
    <FormControl error={shouldShowErrors ? error?.message : undefined}>
      <ComboBox<typeof options[number]>
        aria-label="Trigger dropdown"
        defaultItems={options}
        selectedOption={selectedOption}
        menuTrigger="focus"
        isDisabled={isDisabled || triggers.isLoading}
        icon={
          trigger?.appKey == null ? (
            <Rocket />
          ) : (
            <AppIcon appKey={trigger?.appKey} />
          )
        }
        inputPlaceholder="Select trigger"
        onSelectionChange={(newTrigger) =>
          setData((currentData) => ({
            ...currentData,
            trigger: {
              ...currentData.trigger!,
              triggerKey: newTrigger?.key,
              inputParams: {},
            },
          }))
        }
      >
        {(item) => (
          <ComboBoxItem key={item._comboboxKey} textValue={item.name}>
            <Center className="rollout-listbox__option-icon">
              {trigger?.appKey == null ? (
                <Rocket />
              ) : (
                <AppIcon appKey={trigger?.appKey} />
              )}
            </Center>

            {item.name}
          </ComboBoxItem>
        )}
      </ComboBox>
    </FormControl>
  );
}

export function TriggerKeyField(props: FieldProps) {
  return <TriggerKeyInput isDisabled={props.disabled} />;
}
