import { useQuery } from "@rollouthq/api-client-react";

export function useAppsQuery(opts: { allowedApps: string[] | undefined }) {
  const { allowedApps } = opts;

  return useQuery(["apps.list"], {
    select:
      allowedApps != null
        ? (appsList) => appsList.filter((a) => allowedApps.includes(a.key))
        : undefined,
  });
}
