import { matchSorter } from "match-sorter";
import React, { useState } from "react";

import {
  InferQueryOutput,
  useMutation,
  useTRPCContext,
} from "@rollouthq/api-client-react";
import { AppIcon } from "@rollouthq/connect-react/components/app-icon";
import {
  isChildofInteractiveElement,
  Center,
  Search,
  TextField,
  Spinner,
  Switch,
} from "@rollouthq/ui";

type BlueprintsListItem = InferQueryOutput<"blueprints.list">[number];

export type BlueprintsListProps = {
  blueprints: BlueprintsListItem[];
  isLoading?: boolean;
  onOpenActivationForm: (blueprint: BlueprintsListItem) => void;
};

export function BlueprintsList(props: BlueprintsListProps) {
  const { blueprints, isLoading = false, onOpenActivationForm } = props;

  const [query, setQuery] = useState("");

  const blueprintsFiltered = matchSorter(blueprints, query, {
    keys: ["name", "action.app.name"],
    threshold: matchSorter.rankings.ACRONYM,
  });

  const isFilteredListEmpty = blueprintsFiltered.length === 0;

  return (
    <div className="rollout-automations-list__container">
      <div className="rollout-automations-list__search-input__group">
        <div className="rollout-automations-list__search-input__left-element">
          <Search />
        </div>

        <TextField
          placeholder="Search blueprints"
          value={query}
          onChange={setQuery}
          className="rollout-automations-list__search-input"
        />
      </div>

      {isLoading && (
        <div className="rollout__loading_spinner_container">
          <Spinner className="rollout__loading_spinner" />
        </div>
      )}

      {!isLoading && isFilteredListEmpty && (
        <div className="rollout-automations-list__empty-msg">
          No blueprints found
        </div>
      )}

      <div className="rollout-automations-list__list">
        {blueprintsFiltered.map((blueprint) => (
          <BlueprintsListItem
            key={blueprint.id}
            blueprint={blueprint}
            onOpenActivationForm={() => onOpenActivationForm(blueprint)}
          />
        ))}
      </div>
    </div>
  );
}

type BlueprintsListItemProps = {
  blueprint: BlueprintsListItem;
  onOpenActivationForm: () => void;
};

function BlueprintsListItem(props: BlueprintsListItemProps) {
  const { blueprint, onOpenActivationForm } = props;
  const { invalidateQueries } = useTRPCContext();
  const deactivateAutomation = useMutation("blueprints.deactivateAutomation");
  const active = blueprint.automation?.active ?? false;

  return (
    <div className="rollout-automations-list__item-wrapper">
      <div
        className="rollout-automations-list__item"
        role="group"
        onClick={(e) => {
          if (!isChildofInteractiveElement(e.target as Element)) {
            onOpenActivationForm();
          }
        }}
      >
        <div className="rollout-automations-list-item__header">
          <div className="rollout-automations-list-item__icons">
            <Center className="rollout-automations-list-item__trigger">
              <AppIcon appKey={blueprint.trigger.appKey} />
            </Center>
            <Center className="rollout-automations-list-item__action">
              <AppIcon appKey={blueprint.action.appKey} />
            </Center>
          </div>

          <div className="rollout-automations-list-item__switch">
            <Switch
              isSelected={active}
              onChange={async () => {
                if (active) {
                  const blueprintId = blueprint.id;

                  await deactivateAutomation.mutateAsync({ blueprintId });

                  invalidateQueries(["blueprints.get", { blueprintId }]);
                  invalidateQueries([
                    "blueprints.getAutomation",
                    { blueprintId },
                  ]);
                  invalidateQueries(["blueprints.list"]);
                } else {
                  onOpenActivationForm();
                }
              }}
            />
          </div>
        </div>
        <div className="rollout-automations-list-item__name">
          {blueprint.name}
        </div>
      </div>
    </div>
  );
}
