import React, { ReactNode, useMemo } from "react";

import { useQuery } from "@rollouthq/api-client-react";
import { createContext } from "@rollouthq/ui/";

const [_RolloutAppKeyProvider, useRolloutAppKeyContext] = createContext<{
  appKey: string;
}>({
  name: "RolloutEndUserProvider",
});

export { useRolloutAppKeyContext };

export function RolloutAppKeyProvider(props: { children: ReactNode }) {
  const endUserQuery = useQuery(["end-users.getInfo"]);
  const appKey = endUserQuery.data?.appKey;

  if (appKey == null) {
    return null;
  }

  return (
    <RolloutAppKeyProviderInner appKey={appKey}>
      {props.children}
    </RolloutAppKeyProviderInner>
  );
}

function RolloutAppKeyProviderInner(props: {
  children: ReactNode;
  appKey: string;
}) {
  const { appKey } = props;

  const context = useMemo(() => ({ appKey }), [appKey]);

  return (
    <_RolloutAppKeyProvider value={context}>
      {props.children}
    </_RolloutAppKeyProvider>
  );
}
